import React from "react";
import uniqueId from "lodash/uniqueId";
import isNil from "lodash/isNil";
import isFunction from "lodash/isFunction";

import InputControl from "../InputControl";

import classNames from "classnames";

import "./select.css";

/**
 *   select from a list
 *
 *
 */

class SelectView extends InputControl {

    constructor(props) {
        super(props);
        this.id = uniqueId('select-');

        Object.assign(this.state, {
            selectedValue: this.props.selectedValue
        });

        if(!this.state.selectedValue) {
            let arr = React.Children.toArray(this.props.children)
            if(arr.length > 0) {
                this.state.selectedValue = arr[0].props.value || 0;
            }
        }
    }

    componentWillReceiveProps(newProps) {

        if(!isNil(newProps.selectedValue)) {
            if(newProps.selectedValue !== this.state.selectedValue) {
                this.state.selectedValue = newProps.selectedValue;
            }
        }

        super.componentWillReceiveProps(newProps);
    }

    get selectedIndex() {
        return this._input.selectedIndex;
    }

    set value(v) {
        this.setState({
            selectedValue: v
        });
    }

    get value() {
        return this.state.selectedValue;
    }

    get className() {
        return classNames('v-Select', super.className);
    }

    render() {

        return (
            <div className={this.className}
                 role="listbox"
                 aria-disabled={this.state.disabled}>
                <select id={this.id} value={this.state.selectedValue}
                        name={this.props.name}
                        onChange={this._inputChanged.bind(this)}
                        className="v-Select-input v-no-select"
                        disabled={this.state.disabled}
                        ref={(e) => this._input = e}>
                    {React.Children.map(this.props.children, (child, index) => {
                        return React.cloneElement(child, {
                            key : index,
                            value : child.props.value || index
                        }, child.props.text || child.props.value)
                    })}
                </select>
            </div>
        )
    }

    _inputChanged() {

        this.setState({
            selectedValue: this._input.value
        });

        if (super._inputChanged()) {
            if (isFunction(this.props.action)) {
                this.props.action.call(this, this);
            }

            return true;
        }

        return false;
    }




}


export default SelectView;


