import React from "react";
import ReactDOM from "react-dom";
import isFunction from "lodash/isFunction";
import isNil from "lodash/isNil";

import View from "../View";
import KeyCodes from "../KeyCodes";

import classNames from "classnames";

import "./outline.css";



class ToggleView extends View {

    constructor(props) {
        super(props);
        Object.assign(this.state, {
            hover: false
        });
    }

    get className() {
        return classNames('toggle fa fa-chevron-right', {'is-hover': this.state.hover}, super.className);
    }

    get outlineitem() {
        return this.props.outlineitem;
    }

    render() {
        return (
            <div className={this.className}/>
        )
    }

    onClick(e) {
        e.stopPropagation();

        if (!this.outlineitem.expanded) {
            this.outlineitem.expand();
        }
        else {
            this.outlineitem.collapse();
        }
    }

    mouseOver() {
        this.setState({
            hover: true
        });

    }

    mouseOut() {
        this.setState({
            hover: false
        })
    }
}

class ContentView extends View {

    constructor(props) {
        super(props);


        Object.assign(this.state, {
            hover: false
        });

        document.addEventListener('mousedown', this._detectMouseDown.bind(this), false);
        document.addEventListener('mouseup', this._detectMouseUp.bind(this), false);
    }

    get className() {
        return classNames('content', 'level-' + this.props.level, {
            'is-hover': this.state.hover
        }, super.className);
    }

    get outlineitem() {
        return this.props.outlineitem;
    }

    didLeaveDocument() {
        document.removeEventListener('mousedown', this._detectMouseDown, false);
        document.removeEventListener('mouseup', this._detectMouseUp, false);
    }

    _detectMouseDown() {
        this._md = true;
    }

    _detectMouseUp() {
        this._md = false;
    }

    render() {

        return (
            <div className={this.className} tabIndex={this.props.tabIndex}>
                {this.props.children}
            </div>
        )

    }

    mouseDown(e) {
        e.preventDefault();
    }

    onClick(e) {
        e.stopPropagation();
        this.outlineitem.selected = true;
    }

    mouseOver() {
        if (!this._md) {
            this.setState({
                hover: true
            });
        }
    }

    mouseOut() {
        this.setState({
            hover: false
        });
    }

    keyDown(e) {
        if (e.which === KeyCodes.KEY_SPACE) {

            if (!this.outlineitem.expanded) {
                this.outlineitem.expand()
            }
            else {
                this.outlineitem.collapse();
            }
        }
        else if (e.which === KeyCodes.KEY_ENTER) {
            this.outlineitem.selected = true;
        }
    }
}


/**
 *  OutlineItemView
 *
 *  --- Props ---
 *  icon
 *  text
 *  accessory
 *  selectable
 *
 */
class OutlineItemView extends View {


    constructor(props) {
        super(props);

        Object.assign(this.state, {
            expanded: false,
            selected: false,
            level: 0
        });



    }

    componentWillReceiveProps(newProps) {

        if(!isNil(newProps.expanded)) {
            if(newProps.expanded !== this.expanded) {
                setTimeout(function(){this.expand();}.bind(this), 0);
            }
        }
    }

    didEnterDocument() {
        if(this.props.expanded) {
            this.expand();
        }
    }

    get className() {
        return classNames('v-OutlineItemView', 'v-no-select', {

            'is-selected': this.selected,
            'is-expanded': this.expanded


        }, super.className);
    }

    get outlineview() {
        return this.props.outlineview;
    }

    get text() {
        return this.props.text;
    }

    get level() {
        return this.state.level
    }

    set level(l) {
        this.setState({
            level: l
        });
    }

    get selected() {
        return this.state.selected;
    }

    set selected(flag) {

        if(this.props.selectable) {
            if (this.outlineview.selectedItem) {
                if (this.outlineview.selectedItem !== this) {
                    this.outlineview.selectedItem.selected = false;
                }
            }

            this.setState({
                selected: flag
            });

            this.outlineview.selectedItem = this;
        }
    }

    get expanded() {
        return this.state.expanded;
    }

    set expanded(s) {
        this.setState({
            expanded: s
        })
    }

    get isExpandable() {
        return React.Children.count(this.props.children) > 0;
    }

    render() {

        this._itemRefs = {};
        return (
            <li className={this.className} role="treeitem"
                aria-expanded={this.expanded}>
                <ContentView outlineitem={this} level={this.level}>
                    <ToggleView outlineitem={this} hidden={!this.isExpandable}/>
                    <div className={classNames('icon', this.props.icon)} style={{
                        'display': this.props.icon ? 'inline-block' : 'none'
                    }}/>
                    <div className="label">{this.props.text}</div>
                    <div className="accessory">
                        <div className={this.props.accessory}/>
                    </div>
                </ContentView>
                <div className="collapse">
                    <ul ref="list">
                        {React.Children.map(this.props.children, (item, index) => {
                            return React.cloneElement(item, {
                                ref: (node) => {
                                    // Keep your own reference
                                    this._itemRefs[index] = node;
                                    // Call the original ref, if any
                                    const {ref} = item;
                                    if (isFunction(ref)) {
                                        ref(node);
                                    }
                                },
                                index: index,
                                key: index,
                                outlineview: this.outlineview,
                                tabIndex: this.expanded ? 0 : -1
                            })
                        })}
                    </ul>
                </div>
            </li>
        )
    }

    itemAtIndex(index) {
        return this._itemRefs[index];
    }

    expand() {

        let l = Object.keys(this._itemRefs).length,
            i = 0;

        for (; i < l; i++) {
            let item = this.itemAtIndex(i);

            if (item) {
                item.level = this.level + 1;
            }
        }

        this.expanded = true;

        setTimeout(function () {
            let listEl = ReactDOM.findDOMNode(this.refs.list);
            if(listEl) {
                listEl.style.transform = 'translateY(0);'
            }

        }.bind(this), 0);

    }

    collapse() {

        this.expanded = false;

        setTimeout(function () {
            let el = this.el,
                listEl = ReactDOM.findDOMNode(this.refs.list);
            if(listEl) {
                listEl.style.transform = 'translateY(-' + el.offsetHeight + 'px);'
            }
        }.bind(this), 0);

    }

    _detectMouseDown(e) {
        e.preventDefault();
    }

}

OutlineItemView.defaultProps = {
    selectable: true,
    expanded : false,
};


export default OutlineItemView;


