import React from "react";
import ReactDOM from "react-dom";
import isFunction from "lodash/isFunction";

import Control from "../Control";
import MenuView from "./MenuView";

import classNames from "classnames";

/**
 *
 *  MenuItemView
 *
 *  --- States ---
 *
 *  --- Props ---
 *  text
 *  icon
 *  action
 *
 *
 */

class MenuItemView extends Control {

    static disableMouseOver = false;

    get submenu() {
        return this._submenu;
    }

    get menu() {
        return this.props.menu;
    }

    get className() {
        return classNames('v-MenuItem', 'v-no-select', {
            'v-MenuItem--hasIcon': this.props.icon
        }, super.className);
    }

    render() {

        if (this.props.children) {
            return (

                <li className={this.className} role="menuitem">
                    <i className={classNames('icon', this.props.icon)}/>
                    <div className="label" unselectable="on">{this.props.text}</div>
                    <i className='chevron-right fa fa-chevron-right'/>
                    <MenuView {...this.props.children.props}
                              ref={(e) => this._submenu = e}/>
                </li>

            )
        }

        return (

            <li className={this.className} role="menuitem">
                <i className={classNames('icon', this.props.icon)}/>
                <span className="label" unselectable="on">{this.props.text}</span>
            </li>

        )
    }

    componentDidUpdate() {

        if (!this.state.active) {
            if (this.submenu) {
                this.submenu.hide();
            }
        }

        let currentActive = this.props.menu.activeItem;
        if (currentActive) {
            if (this !== currentActive) {
                if (this.state.active) {
                    currentActive.setState({active: false});
                    this.menu.activeItem = this;
                    this._displaySubmenu();
                }
            }
        }
        else {
            if (this.state.active) {
                this.menu.activeItem = this;
                this._displaySubmenu();
            }
            else {
                this.menu.activeItem = null;
            }
        }

    }

    _displaySubmenu() {

        let submenu = this.submenu;
        if (submenu) {

            submenu.superMenu = this.menu;

            let el = this.el,
                submenuEl = ReactDOM.findDOMNode(submenu),
                rect = el.getBoundingClientRect(),
                left = rect.left + el.offsetWidth + 1;


            if (left + submenuEl.offsetWidth > document.body.offsetWidth - 4) {
                left = Math.max(0, rect.left - submenuEl.offsetWidth - 1);
            }

            submenu.show(left, rect.top - 1);
        }

    }

    select() {

        if (this.state.disabled)
            return;

        if (!this.submenu) {

            if (this.menu) {
                this.menu.close();
            }

            if (isFunction(this.props.action)) {
                this.props.action.call(this);
            }
        }
    }

    mouseOver(e) {
        e.preventDefault();
        if (!MenuItemView.disableMouseOver) {
            this.setState({
                active: true
            });

            if (!this.submenu) {
                this.menu.focus();
            }
        }

    }

    mouseOut() {
        if (!this.submenu)
            this.setState({active: false});

    }

    onClick() {
        this.select();
    }

    onTap() {
        this.select();
    }


}

export default MenuItemView;


