import ReactDOM from "react-dom";
import isFunction from "lodash/isFunction";
import findIndex from "lodash/findIndex";

/**
 *
 *  adds 'tap' event recognition on touch devices
 *
 */
class TapRecognizer {

    constructor(target) {

        this.t = target; //target
        this.d = 15; //distance

        this.touchStart = this.touchStart.bind(this);
        this.touchMove = this.touchMove.bind(this);
        this.touchCancel = this.touchCancel.bind(this);
        this.touchEnd = this.touchEnd.bind(this);

    }

    touchStart(e) {

        this.tC = false;  //touch cancelled;
        this.m = false;   //has moved

        let touch = e.targetTouches[0];
        this.sx = touch.pageX; //start-x
        this.sy = touch.pageY; //start-y;

    }

    touchMove(e) {
        let touch = e.targetTouches[0];
        if (Math.abs(touch.pageX - this.sx) > this.d
            || Math.abs(touch.pageY - this.sy) > this.d
        ) {
            this.m = true;
        }
    }

    touchEnd(e) {
        if (!this.m && !this.tC) {
            if (this.t.onTap) {
                this.t.onTap.call(this.t, e);
            }
        }
    }

    touchCancel() {
        this.tC = true;
    }
}

/**
 *  EventResponder.js
 *
 *  add event responding to View and View subclasses
 *
 */
export default class EventResponder {

    static notifications = {}

    /** Initalize event responder on a View
     *
     * @param Obj the object responding to the events
     * @param View the View receiving the events
     */

    static init(Obj, View) {

        if (Obj.mouseDown) {
            Obj.mouseDown = Obj.mouseDown.bind(Obj);
        }

        if (Obj.mouseUp) {
            Obj.mouseUp = Obj.mouseUp.bind(Obj);
        }

        if (Obj.mouseMove) {
            Obj.mouseMove = Obj.mouseMove.bind(Obj);
        }

        if (Obj.mouseOver) {
            Obj.mouseOver = Obj.mouseOver.bind(Obj);
        }

        if (Obj.mouseOut) {
            Obj.mouseOut = Obj.mouseOut.bind(Obj);
        }

        if (Obj.onClick) {
            Obj.onClick = Obj.onClick.bind(Obj);
        }

        if (Obj.onDoubleClick) {
            Obj.onDoubleClick = Obj.onDoubleClick.bind(Obj);
        }

        if (Obj.contextMenu) {
            Obj.contextMenu = Obj.contextMenu.bind(Obj);
        }

        if (Obj.keyDown) {
            Obj.keyDown = Obj.keyDown.bind(Obj);
        }

        if (Obj.keyUp) {
            Obj.keyUp = Obj.keyUp.bind(Obj);
        }

        if (Obj.keyPress) {
            Obj.keyPress = Obj.keyPress.bind(Obj);
        }

        if (Obj.onFocus) {
            Obj.onFocus = Obj.onFocus.bind(Obj);
        }

        if (Obj.onBlur) {
            Obj.onBlur = Obj.onBlur.bind(Obj);
        }

        if(Obj.onScroll) {
            Obj.onScroll = Obj.onScroll.bind(Obj);
        }

        if (Obj.touchStart) {
            Obj.touchStart = Obj.touchStart.bind(Obj);
        }

        if (Obj.touchEnd) {
            Obj.touchEnd = Obj.touchEnd.bind(Obj);
        }

        if (Obj.touchCancel) {
            Obj.touchCancel = Obj.touchCancel.bind(Obj);
        }

        if (Obj.touchMove) {
            Obj.touchMove = Obj.touchMove.bind(Obj);
        }

        if (Obj.onTap) {
            Obj.onTap = Obj.onTap.bind(Obj);
        }

        if (Obj.onResize) {
            Obj.onResize = Obj.onResize.bind(Obj);
        }

        if (View) {

            let Element = ReactDOM.findDOMNode(View);

            if (!Element) return;

            if (Obj.mouseDown)
                Element.addEventListener('mousedown', Obj.mouseDown);

            if (Obj.mouseUp)
                Element.addEventListener('mouseup', Obj.mouseUp);

            if (Obj.mouseOver) {
                Element.addEventListener('mouseover', Obj.mouseOver);
            }

            if (Obj.mouseOut) {
                Element.addEventListener('mouseout', Obj.mouseOut);
            }

            if (Obj.mouseMove) {
                Element.addEventListener('mousemove', Obj.mouseMove);
            }

            if (Obj.onClick) {
                Element.addEventListener('click', Obj.onClick);
            }

            if (Obj.onDoubleClick) {
                Element.addEventListener('dblclick', Obj.onDoubleClick);
            }

            if (Obj.contextMenu) {
                Element.addEventListener('contextmenu', Obj.contextMenu);
            }

            if (Obj.keyDown) {
                Element.addEventListener('keydown', Obj.keyDown);
            }

            if (Obj.keyUp) {
                Element.addEventListener('keyup', Obj.keyUp);
            }

            if (Obj.keyPress) {
                Element.addEventListener('keypress', Obj.keyPress)
            }

            if (Obj.onFocus) {
                Element.addEventListener('focus', Obj.onFocus);
            }

            if (Obj.onBlur) {
                Element.addEventListener('blur', Obj.onBlur);
            }

            if(Obj.onScroll) {
                Element.addEventListener('scroll', Obj.onScroll);
            }

            if (Obj.touchStart) {
                Element.addEventListener('touchstart', Obj.touchStart);
            }

            if (Obj.touchEnd) {
                Element.addEventListener('touchend', Obj.touchEnd);
            }

            if (Obj.touchCancel) {
                Element.addEventListener('touchcancel', Obj.touchCancel);
            }

            if (Obj.touchMove) {
                Element.addEventListener('touchmove', Obj.touchMove);
            }

            if (Obj.onTap) {
                Obj.tapRecognizer = new TapRecognizer(Obj);
                Element.addEventListener('touchstart', Obj.tapRecognizer.touchStart);
                Element.addEventListener('touchend', Obj.tapRecognizer.touchEnd);
                Element.addEventListener('touchmove', Obj.tapRecognizer.touchMove);
                Element.addEventListener('touchcancel', Obj.tapRecognizer.touchCancel);
            }


        }
    }

    /**
     *  releases event responding (used for clean up)
     *
     * @param Obj
     * @param View
     */

    static release(Obj, View) {

        if (View) {

            let Element = ReactDOM.findDOMNode(View);

            if (!Element) return;

            if (Obj.mouseDown)
                Element.removeEventListener('mousedown', Obj.mouseDown);

            if (Obj.mouseUp)
                Element.removeEventListener('mouseup', Obj.mouseUp);

            if (Obj.mouseOver) {
                Element.removeEventListener('mouseover', Obj.mouseOver);
            }

            if (Obj.mouseOut) {
                Element.removeEventListener('mouseout', Obj.mouseOut);
            }

            if (Obj.mouseMove) {
                Element.removeEventListener('mousemove', Obj.mouseMove);
            }

            if (Obj.onClick) {
                Element.removeEventListener('click', Obj.onClick);
            }

            if (Obj.onDoubleClick) {
                Element.removeEventListener('dblclick', Obj.onDoubleClick);
            }

            if (Obj.contextMenu) {
                Element.removeEventListener('contextmenu', Obj.contextMenu);
            }

            if (Obj.keyDown) {
                Element.removeEventListener('keydown', Obj.keyDown);
            }

            if (Obj.keyUp) {
                Element.removeEventListener('keyup', Obj.keyUp);
            }

            if (Obj.keyPress) {
                Element.removeEventListener('keypress', Obj.keyPress);
            }

            if (Obj.onFocus) {
                Element.removeEventListener('focus', Obj.onFocus);
            }

            if (Obj.onBlur) {
                Element.removeEventListener('blur', Obj.onBlur);
            }

            if(Obj.onScroll) {
                Element.removeEventListener('scroll', Obj.onScroll);
            }

            if (Obj.touchStart) {
                Element.removeEventListener('touchstart', Obj.touchStart);
            }

            if (Obj.touchEnd) {
                Element.removeEventListener('touchend', Obj.touchEnd);
            }

            if (Obj.touchCancel) {
                Element.removeEventListener('touchcancel', Obj.touchCancel);
            }

            if (Obj.touchMove) {
                Element.removeEventListener('touchmove', Obj.touchMove);
            }

            if (Obj.onTap && Obj.tapRecognizer) {
                Element.removeEventListener('touchstart', Obj.tapRecognizer.touchStart);
                Element.removeEventListener('touchend', Obj.tapRecognizer.touchEnd);
                Element.removeEventListener('touchmove', Obj.tapRecognizer.touchMove);
                Element.removeEventListener('touchcancel', Obj.tapRecognizer.touchCancel);
            }
        }
    }

    /**
     * Allows an Object to register to listen and respond to a notification
     *
     * @param notificationName
     * @param Obj - the listening object
     * @param callback - the method called on the object
     */

    static listenFor(notificationName, Obj, callback) {
        let notif = EventResponder.notifications;
        if (!notif[notificationName]) {
            notif[notificationName] = [];
        }
        notif[notificationName].push({obj: Obj, fn: callback});
    }


    static removeListener(notificationName, Obj) {
        let notif = EventResponder.notifications,
            arr = notif[notificationName];
        if (arr) {
            let idx = findIndex(arr, {obj : Obj});
            if(idx > -1) {
                arr.splice(idx, 1);
                if(arr.length === 0) {
                    delete notif[notificationName];
                }
            }
        }
    }

    /**
     * Broadcast a notification to all listeners
     * @param notificationName
     */

    static postNotification(notificationName) {

        let notif = EventResponder.notifications;
        if (notif[notificationName]) {
            let listeners = notif[notificationName];
            for (var idx in listeners) {
                let n = listeners[idx];
                if (isFunction(n.fn)) {
                    n.fn.call(n.obj);
                }
            }
        }
    }

}


