import React from "react";
import isFunction from "lodash/isFunction";

import View from "../View";

import classNames from "classnames";

import "./outline.css";


/**
 *
 *  OutlineView
 *
 *
 *  --- Props ---
 *  onSelect
 *
 *
 */

class OutlineView extends View {



    get className() {
        return classNames('v-OutlineView', super.className);
    }

    get selectedItem() {
        return this._sItem;
    }

    set selectedItem(s) {
        if (this._sItem !== s) {
            this._sItem = s;
            if (isFunction(this.props.onSelect)) {
                this.props.onSelect.call(this, this._sItem);
            }
        }

    }

    render() {

        this._itemRefs = {};
        return (

            <div className={this.className} role="tree">
                <ul>
                    {React.Children.map(this.props.children, (item, index) => {
                        return React.cloneElement(item, {
                            ref: (node) => {
                                // Keep your own reference
                                this._itemRefs[index] = node;
                                // Call the original ref, if any
                                const {ref} = item;
                                if (isFunction(ref)) {
                                    ref(node);
                                }
                            },
                            index: index,
                            key: index,
                            outlineview: this,
                            tabIndex: 0
                        })
                    })}
                </ul>


            </div>
        )
    }

    itemAtIndex(index) {
        if (index > -1 && index < this.state.items.length) {
            return this._itemRefs[index];
        }
    }
}


export default OutlineView;


