import isString from "lodash/isString";

import TextView from "./TextView";
import KeyCodes from "../../KeyCodes";



class MaskedTextView extends TextView {

    static definitions = {
        '9': '[0-9]',
        'a': '[A-Za-z]',
        '*': '[A-Za-z0-9]'
    };

    constructor(props) {
        super(props);

        this.maskPlaceholder = "_";
        this.fNMP = -1;
        this._handleCutPasteChange = this._handleCutPasteChange.bind(this);
        this._checkVal = this._checkVal.bind(this);
    }

    renderControl() {
        if (this.props.multiline) {
            throw new Error('MaskedTextView does not support multiline.');
        }

        return super.renderControl();
    }


    didEnterDocument() {
        super.didEnterDocument();

        this._input.addEventListener('cut', this._handleCutPasteChange);
        this._input.addEventListener('paste', this._handleCutPasteChange);

        this._parseMask();
    }

    set value(v) {

        if (this._input)
            this._input.value = v;

        this._readBuffer();
    }

    get value() {
        if (this._input)
            return this._input.value;
    }


    _caret(begin, end) {

        if (this.value.length === 0 || this.props.hidden || !this.state.focused) {
            return [0, 0];
        }

        if (begin > -1) {
            end = (end > -1) ? end : begin;
            this.setSelectionRange(begin, end);
        } else {
            begin = this.selectionStart();
            end = this.selectionEnd();
        }

        return [begin, end];
    }

    _parseMask() {

        if (this.props.mask) {

            console.log("ppp = " + this.props.mask);

            let mask = this.props.mask;

            this.len = mask.length;
            this.pP = this.len;
            this.fNMP = -1;

            this.tests = [];
            this.buffer = [];

            let i = 0, count = this.len;

            for (; i < count; i++) {

                let c = mask.charAt(i);

                if (c === '?') {
                    this.len--;
                    this.pP = i;
                }
                else if (MaskedTextView.definitions[c]) {
                    this.tests.push(new RegExp(MaskedTextView.definitions[c]));
                    if (this.fNMP === -1) {
                        this.fNMP = this.tests.length - 1;
                    }
                }
                else {
                    this.tests.push(null);
                }

                if (c !== '?') {
                    let b = MaskedTextView.definitions[c] ? this._getPlaceholder(i) : c;
                    this.buffer.push(b);
                }
            }

            this.dBuffer = this._bufferAsString();
            this.focusText = this.value;

        }

    }

    _handleCutPasteChange() {

        setTimeout(function () {
            let pos = this._checkVal(true);
            this._caret(pos, -1);

        }.bind(this), 0);

    }

    _bufferAsString() {
        let bufferStr = "",
            i = 0,
            count = this.buffer.length;

        for (; i < count; i++) {
            bufferStr += this.buffer[i];
        }

        return bufferStr;
    }

    _getPlaceholder(pos) {
        if (pos < this.maskPlaceholder.length) {
            return this.maskPlaceholder.charAt(pos);
        }

        return this.maskPlaceholder.charAt(0);
    }

    _seekNext(pos) {
        while (++pos < this.len && !this.tests[pos]) ;
        return pos;
    }

    _seekPrev(pos) {
        while (--pos >= 0 && !this.tests[pos]) ;
        return pos;
    }


    _shiftL(begin, end) {

        if (begin < 0) {
            return;
        }

        let i = begin,
            j = this._seekNext(end);

        for (; i < this.len; i++) {

            if (this.tests[i]) {
                if (j < this.len && this.tests[i].test(this.buffer[j])) {
                    this.buffer[i] = this.buffer[j];
                    this.buffer[j] = this._getPlaceholder(j);
                } else {
                    break;
                }

                j = this._seekNext(j);
            }
        }

        this._writeBuffer();
        this._caret(Math.max(this.fNMP, begin), -1);
    }


    _shiftR(pos) {

        let i = pos,
            j = -1,
            t = null,
            c = this._getPlaceholder(pos),
            count = this.len;

        for (; i < count; i++) {
            if (this.tests[i]) {
                j = this._seekNext(i);
                t = this.buffer[i];

                this.buffer[i] = c;

                if (j < this.len && this.tests[j].test(t)) {
                    c = t;
                } else {
                    break;
                }
            }
        }
    }

    _clearBuffer(start, end) {
        let i = start;
        for (; i < end && i < this.len; i++) {
            if (i > -1) {
                if (this.tests[i]) {
                    this.buffer[i] = this._getPlaceholder(i);
                }
            }
        }
    }

    _readBuffer() {

        let i = 0,
            l = this.value.length;

        for(; i < l; i++) {
            this.buffer[i] = this.value[i];
        }

    }

    _writeBuffer() {
        this._input.value = this._bufferAsString();
    }

    _checkVal(allow) {

        let test = this.value,
            lastMatch = -1,
            i = 0,
            pos = 0,
            c,
            count = this.len;

        if (isString(test)) {
            for (; i < count; i++) {
                if (this.tests[i]) {

                    this.buffer[i] = this._getPlaceholder(i);
                    while (pos++ < test.length) {
                        c = "" + test.charAt(pos - 1);
                        if (this.tests[i].test(c)) {
                            this.buffer[i] = c;
                            lastMatch = i;
                            break;
                        }
                    }

                    if (pos > test.length) {
                        this._clearBuffer(i + 1, this.len);
                        break;
                    }
                } else {
                    if (this.buffer[i] === test.charAt(pos)) {
                        pos++;
                    }
                    if (i < this.pP) {
                        lastMatch = i;
                    }
                }
            }

            if (allow) {
                this._writeBuffer();
            } else if (lastMatch + 1 < this.pP) {

                if (this._bufferAsString() === this.dBuffer) {
                    this.value = "";
                    this._clearBuffer(0, this.len);
                } else {
                    this._writeBuffer();
                }
            } else {
                this._writeBuffer();
                this.value = this.value.substring(0, lastMatch + 1)
            }

            return (this.pP > 0 ? i : this.fNMP);
        }

        return false;

    }

    keyDown(event) {

        let k = event.which, begin, end,
            pos = [];

        if (k === KeyCodes.KEY_BACKSPACE || k === KeyCodes.KEY_DELETE) {

            pos = this._caret(-1, -1);
            begin = pos[0];
            end = pos[1];

            if (end - begin === 0) {
                begin = (k !== KeyCodes.KEY_DELETE) ? this._seekPrev(begin) : (end = this._seekNext(begin - 1));
                end = (k === KeyCodes.KEY_DELETE) ? this._seekNext(end) : end;
            }

            this._clearBuffer(begin, end);
            this._shiftL(begin, end - 1);

            event.preventDefault();

            this._inputChanged();

        } else if (k === KeyCodes.KEY_ENTER) {
            this.onBlur(event);
        } else if (k === KeyCodes.KEY_ESCAPE) {
            this.value = this.focusText;
            this._caret(0, this._checkVal(false));
            event.preventDefault();
        }

    }

    keyPress(event) {

        let k = event.which,
            p,
            next = -1,
            c,
            pos = this._caret(-1, -1),
            begin = pos[0],
            end = pos[1];

        if (event.ctrlKey || event.altKey || event.metaKey || k < 32) { //ignore
            return;
        } else if (k > 0 && k !== KeyCodes.KEY_ENTER) {

            if (end - begin !== 0) {
                this._clearBuffer(begin, end);
                this._shiftL(begin, end - 1);
            }

            p = this._seekNext(begin - 1);
            if (p < this.len) {
                c = String.fromCharCode(k);
                if (this.tests[p].test(c)) {
                    this._shiftR(p);
                    this.buffer[p] = c;
                    this._writeBuffer();
                    next = this._seekNext(p);
                }

                this._caret(next, -1);

            }

            event.preventDefault();
            this._inputChanged();

        }


    }

    onFocus() {

        if (this.caretTimer) {
            clearTimeout(this.caretTimer);
            this.caretTimer = null;
        }

        let mask = this.props.mask;
        if (mask) {

            this.focusText = this.value;

            let pos = this._checkVal(false);

            this._writeBuffer();

            this.caretTimer = setTimeout(function () {

                if (!this.state.focused) {
                    return;
                }

                if (pos === mask.replace("?", "").length) {
                    this._caret(0, pos);
                } else {
                    this._caret(pos, -1);
                }

            }.bind(this), 10);

        }
    }

    onBlur() {
        this._checkVal(false);
    }


}


export default MaskedTextView;


