import React from 'react';
import isFunction from 'lodash/isFunction';
import classname from 'classnames';

import View from '../View';
import ButtonView from '../button/ButtonView';

import './pager.css';


class Pager extends View {

    constructor(props) {
        super(props);

         Object.assign(this.state, {
             startPage:0,
             table: null
         });
    }

    render() {

        if(this.state.table) {

            let table = this.state.table,
                buttonCount = this.props.buttonCount,
                page = this.state.startPage,
                endPage = Math.min(page+buttonCount, table.pageCount),
                btns = [];

            if(endPage - page > 0 && endPage > 1) {
                for(; page < endPage; page++) {
                    btns.push(
                        <ButtonView text={(page+1)}
                                    key={page}
                                    page={page}
                                    className={classname({
                                        'selected' : table.page === page,
                                        'first' : page === 0 && table.pageCount <= buttonCount
                                    })}
                                    type={table.page === page ? 'primary' : 'default'}
                                    action={this.changePage.bind(this)}
                        />
                    )
                }

                let prevBtn = null,
                    nextBtn = null;
                if(table.pageCount > buttonCount) {
                    prevBtn = <ButtonView className="first" disabled={this.state.startPage === 0} iconOnly={true} icon="fa fa-angle-left" action={this.prevFive.bind(this)}/>
                    nextBtn = <ButtonView disabled={this.state.startPage+buttonCount+1 > table.pageCount} iconOnly={true} icon="fa fa-angle-right" action={this.nextFive.bind(this)} />
                    btns.push(nextBtn);
                }

                return (
                    <div className="v-Pager">
                        {prevBtn}
                        {btns}
                    </div>
                )
            }
        }

        return null;

    }



    get table() {
        return this.state.table;
    }

    set table(t) {
        this.setState({
            table: t
        });
    }

    didEnterDocument() {
        if(isFunction(this.props.table)) {
            this.table = this.props.table();
        }
    }

    changePage(sender) {
        this.state.table.page = sender.props.page;
        this.setState({
            table: this.state.table
        });

        if(isFunction(this.props.onPageChange)) {
            this.props.onPageChange.call(this, this.state.table.page);
        }
    }

    nextFive() {

        let nextStart = this.state.startPage+this.props.buttonCount
        this.state.table.page = nextStart;
        this.setState({
            startPage: nextStart
        });



    }

    prevFive() {

        let prevStart = Math.max(0, this.state.startPage-this.props.buttonCount);

        this.state.table.page = prevStart+this.props.buttonCount-1;

        this.setState({
            startPage:prevStart
        });
    }



}

Pager.defaultProps = {buttonCount:5};


export default Pager;


