import React from "react";
import ReactDOM from "react-dom";
import View from "../View.js";

import classNames from "classnames";
import "./popover.css";


export const Edge = Object.freeze({
    TOP: 1,
    BOTTOM: 2,
    RIGHT: 3,
    LEFT: 4,
    MIDDLE: 5,
    CENTER: 6
});

export const Position = Object.freeze({
    TOP: 1,
    BOTTOM: 2,
    RIGHT: 3,
    LEFT: 4
});

class PopOverView extends View {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            open: false
        });


        this.show = this.show.bind(this);
        this.close = this.close.bind(this);

    }

    get className() {
        return classNames(
            'v-PopOver',
            {
                'closed': !this.state.open,
            },
            super.className);
    }

    render() {

        return ReactDOM.createPortal(
            <div className={this.className} role="dialog" tabIndex={this.props.tabIndex}>
                {this.props.children}
                <b className={this._getCalloutClassName()} ref={(e) => this._callout = e}>
                    <b className={this._getCalloutTriangleClassName()} ref={(e) => this._triangle = e}/>
                </b>
            </div>,
            document.body);

    }

    _getCalloutClassName() {
        let position = this.props.position,
            edge = this.props.edge;
        return classNames('v-PopOver-callout', {
            'v-PopOver-callout-none': !this.props.callout,
            'v-PopOver-callout--t': position === Position.TOP,
            'v-PopOver-callout--b': position === Position.BOTTOM,
            'v-PopOver-callout--r': position === Position.RIGHT,
            'v-PopOver-callout--l': position === Position.LEFT,
            'v-PopOver-callout--top': edge === Edge.TOP,
            'v-PopOver-callout--bottom': edge === Edge.BOTTOM,
            'v-PopOver-callout--right': edge === Edge.RIGHT,
            'v-PopOver-callout--left': edge === Edge.LEFT,
            'v-PopOver-callout--middle': edge === Edge.MIDDLE,
            'v-PopOver-callout--center': edge === Edge.CENTER
        });

    }

    _getCalloutTriangleClassName() {
        let position = this.props.position;
        return classNames('v-PopOver-triangle', {
            'v-PopOver-triangle-none': !this.props.callout,
            'v-PopOver-triangle--t': position === Position.TOP,
            'v-PopOver-triangle--b': position === Position.BOTTOM,
            'v-PopOver-triangle--r': position === Position.RIGHT,
            'v-PopOver-triangle--l': position === Position.LEFT
        })


    }

    /**
     *
     * @param alignWithView - view popover is aligned
     * @param offsetLeft
     * @param offsetTop
     */
    show(alignWithView, offsetLeft, offsetTop) {

        let position = this.props.position,
            edge = this.props.edge;

        let atNode = ReactDOM.findDOMNode(alignWithView),
            atNodeWidth = atNode.offsetWidth + 7,
            atNodeHeight = atNode.offsetHeight,
            rect = atNode.getBoundingClientRect(),
            left = rect.left,
            top = rect.top;


        switch (position) {
            case Position.RIGHT:
                left += atNodeWidth;
            /* falls through */
            case Position.LEFT:
                switch (edge) {
                    // case 'top':
                    //    break; // nothing to do
                    case  Edge.MIDDLE:
                        atNodeHeight = atNodeHeight >> 1;
                    /* falls through */
                    case Edge.BOTTOM:
                        top += atNodeHeight;
                        break;
                    default: {
                    }
                }
                break;
            case Position.BOTTOM:
                top += atNodeHeight;
            /* falls through */
            case Position.TOP:
                switch (edge) {
                    // case 'left':
                    //     break; // nothing to do
                    case Edge.CENTER:
                        atNodeWidth = atNodeWidth >> 1;
                    /* falls through */
                    case Edge.RIGHT:
                        left += atNodeWidth;
                        break;
                    default: {
                    }
                }
                break;
            default: {
            }
        }

        top += offsetTop || 0;
        left += offsetLeft || 0;

        let layer = this.el;

        layer.style.left = left + 'px';
        layer.style.top = top + 'px';

        let deltaLeft = 0,
            deltaTop = 0;

        // Adjust positioning
        switch (position) {
            case Position.LEFT:
                deltaLeft -= layer.offsetWidth;
                deltaLeft -= 8;
            /* falls through */
            case Position.RIGHT:
                switch (edge) {
                    // case 'top':
                    //    break; // nothing to do
                    case Edge.MIDDLE:
                        deltaTop -= layer.offsetHeight >> 1;
                        break;
                    case Edge.BOTTOM:
                        deltaTop -= layer.offsetHeight;
                        break;
                    default: {
                    }
                }
                break;
            case Position.TOP:
                deltaTop -= layer.offsetHeight;
                deltaTop -= 18;
            /* falls through */
            case Position.BOTTOM:
                deltaTop += 10;
                switch (edge) {
                    case Edge.CENTER:
                        deltaLeft -= layer.offsetWidth >> 1;
                        break;
                    case Edge.RIGHT:
                        deltaLeft -= layer.offsetWidth;
                        break;
                    default: {
                    }
                }
                break;
            default: {
            }
        }

        this._adjustPosition(deltaLeft, deltaTop);

        let triangle = this._triangle,
            bC = this.props.backgroundColor,
            borderColor = this.props.borderColor;
        triangle.style.backgroundColor = bC;
        triangle.style.borderColor = borderColor;
        layer.style.backgroundColor = bC;
        layer.style.borderColor = borderColor;


        this.open();

    }


    _adjustPosition(deltaLeft, deltaTop) {

        let el = this.el,
            calloutDelta = 0,
            position = this.props.position,
            calloutIsAtTopOrBottom = (position === Position.TOP || position === Position.BOTTOM),
            rect = el.getBoundingClientRect(),
            left = rect.left,
            top = rect.top;


        let gap = document.body.offsetWidth - left - deltaLeft - el.offsetWidth;

        if (gap < 0) {
            deltaLeft += gap;
            deltaLeft -= 7;
            if (this.props.callout && calloutIsAtTopOrBottom) {
                calloutDelta += gap;
                calloutDelta -= 7;
            }
        }

        //check left edge
        gap = left + deltaLeft;
        if (gap < 0) {
            deltaLeft -= gap;
            deltaLeft += 7;
            if (this.props.callout && calloutIsAtTopOrBottom) {
                calloutDelta -= gap;
                calloutDelta += 7;
            }
        }

        //check bottom edge
        gap = document.body.offsetHeight - top - deltaTop - el.offsetHeight;
        if (gap < 0) {
            deltaTop += gap;
            deltaTop -= 7;
            if (this.props.callout && !calloutIsAtTopOrBottom) {
                calloutDelta += gap;
                calloutDelta -= 4;
            }
        }

        //check top edge
        gap = top + deltaTop;
        if (gap < 0) {
            deltaTop -= gap;
            deltaTop += 7;
            if (this.props.callout && !calloutIsAtTopOrBottom) {
                calloutDelta -= gap;
                calloutDelta += 4;
            }
        }

        top += deltaTop;
        left += deltaLeft;

        el.style.top = (top) + 'px';
        el.style.left = (left) + 'px';

        if (this.props.callout && calloutDelta !== 0) {

            let callout = this._callout;
            if (calloutIsAtTopOrBottom) {

                if (calloutDelta < 0) {
                    callout.style.left = Math.min(-calloutDelta, el.offsetWidth / 3) + 'px';
                }
                else {
                    callout.style.left = Math.max(-calloutDelta, -el.offsetWidth / 3) + 'px';
                }
            }
            else {

                if (calloutDelta < 0) {
                    callout.style.top = Math.min(-calloutDelta, el.offsetHeight / 3) + 'px';
                }
                else {
                    callout.style.top = Math.max(-calloutDelta, -el.offsetHeight / 3) + 'px';
                }
            }
        }
    }

    didEnterDocument() {
        if (this.props.transient) {
            document.addEventListener('mousedown', this.close);
        }
    }

    didLeaveDocument() {
        document.removeEventListener('mousedown', this.close);
    }

    open() {
        this.setState({
            open: true
        });

        if (this.props.onOpen) {
            this.props.onOpen.call(this);
        }
    }

    close() {
        this.setState({
            open: false
        });

        if (this.props.onClose) {
            this.props.onClose.call(this);
        }
    }


    mouseDown(e) {
        e.stopPropagation();
    }
}

PopOverView.defaultProps = {
    callout: true,
    transient: true,
    backgroundColor: '#fff',
    borderColor: '#bbb'
};

export default PopOverView;


