import React from 'react';
import slice from 'lodash/slice';

import TableView from './TableView';
import TableViewHeader from "./TableViewHeader";
import TableViewBody from "./TableViewBody";


class PaginatedTableView extends TableView {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            page: 0
        });

    }

    render() {

        let rCount = this.props.recordsPerPage,
            start = rCount*this.page;

        let pageData = slice(this.data, start, start+rCount);

        let tableHeader = null;

        if(this.props.hasHeader) {
            tableHeader = <TableViewHeader scrollLeft={this.state.scrollLeft}
                                           table={this}
                                           sortInfo={this.state.sortInfo}
                                           headerHeight={this.props.headerHeight}
                                           align={this.props.headerAlign}
                                           ref={e => this._tableHeader = e}/>
        }

        return (
            <div role="grid" className={this.className}>
                {tableHeader}
                <TableViewBody table={this}
                               summary={this.props.summary}
                               emptyText={this.props.emptyText}
                               data={pageData}
                               className={(this.props.hasHeader ? '' : 'noHeader')}
                               ref={e => this._tableBody = e}/>
            </div>
        )

    }

    get page() {
        return this.state.page;
    }

    set page(p) {
        this.setState({
            page: p
        });
    }

    get pageCount() {
        if(this.data) {
            return Math.ceil(this.data.length/this.props.recordsPerPage);
        }

        return 0;
    }

    get recordsPerPage() {
        return this.props.recordsPerPage;
    }



}

PaginatedTableView.defaultProps = {
    recordsPerPage: 25,
    headerHeight:32,
    headerAlign:"center",
    hasHeader:true,
    emptyText: "No Data",
    columns: []
};


export default PaginatedTableView;


