import React from 'react';
import ReactDOM from 'react-dom';

import isFunction from 'lodash/isFunction';
import className from 'classnames';

import View from '../View';
import ScrollView from '../ScrollView';

import classNames from 'classnames';

class TableViewBody extends View {
    render() {

        if(this.loading) {
            return (
                <div
                    className={this.className} >
                    <div className="v-loading"/>
                </div>
            )
        }

        let table = this.props.table,
            data = this.props.data;
        if(data && data.length > 0) {
            let tableBody =
                (
                    <tbody>
                        {this.props.data.map(function(row, rIdx){
                            return (
                                <tr key={rIdx} className={className({
                                    'row-odd' : rIdx % 2 !== 0,
                                    'row-last' : rIdx === data.length - 1
                                })}>
                                    {table.props.columns.map(function(column, cIdx){
                                        let content = row[column.id];
                                        if(isFunction(column.formatter)){
                                            content = column.formatter(content, row);
                                        }
                                        let cWidth = (table.state.columnWidths[column.id] || 100);
                                        return (
                                            <td key={"r"+rIdx+"c"+cIdx}
                                                style={{minWidth: cWidth, width:cWidth, maxWidth:cWidth }}>{content}</td>
                                        )
                                    })}
                                </tr>
                            )
                        })}
                    </tbody>
                );

            return (
                <ScrollView
                    scroll={table._onTableScroll.bind(table)}
                    className={this.className} >
                    <table summary={this.props.summary} ref={e => this._table = e}>
                        {tableBody}
                    </table>
                </ScrollView>
            )
        }
        else {

            return (
                    <ScrollView
                        className={this.className} >
                        <div className="v-TableEmptyText">{this.props.emptyText}</div>
                    </ScrollView>
                )

        }


    }

    get loading() {
        return this.props.table.state.loading;
    }

    get scrollHeight() {
        if(this._table)
            return ReactDOM.findDOMNode(this._table).offsetHeight;
        return 0;
    }

    get className() {
        return classNames('v-TableViewBody', {
             'empty' : !this.props.data  || (this.props.data && this.props.data.length===0) || this.loading
        }, super.className);
    }

}


export default TableViewBody;


