import ReactDOM from "react-dom";
import isFunction from "lodash/isFunction";

import ListView from "./ListView";
import EventResponder from "../EventResponder";
import classNames from "classnames";

import "./list.css";


class ProgessiveListView extends ListView {

    constructor(props) {
        super(props);

        if (!isFunction(this.itemHeight)) {
            throw new Error("Make sure to implement 'itemHeight()' on the list");
        }

        this.state.renderRange.end = 0;
        this.visibleRectDidChange = this.visibleRectDidChange.bind(this);

        EventResponder.listenFor("resize", this, this.onResize);
    }


    get className() {
        return classNames('v-ProgressiveList', super.className);
    }

    componentDidUpdate() {
        if (this.content) {
            let el = ReactDOM.findDOMNode(this._content);
            el.style.height = this.content.length * this.itemHeight() + 'px';
        }

        this.visibleRectDidChange();
        this.layout();

    }

    didEnterDocument() {
        this.componentDidUpdate();
        ReactDOM.findDOMNode(this._scroller)
            .addEventListener('scroll', this.visibleRectDidChange, false);
    }

    onResize() {


        if (this.isInDocument) {
            let el = this.el;
            if (this._lastW !== el.offsetWidth || this._lastH !== el.offsetHeight) {
                this._lastW = el.offsetWidth;
                this._lastH = el.offsetHeight;
                this.visibleRectDidChange();
            }
        }

    }

    didLeaveDocument() {
        EventResponder.removeListener('resize', this);
        ReactDOM.findDOMNode(this._scroller)
            .removeEventListener('scroll', this.visibleRectDidChange, false);
        clearInterval(this.dimInterval);
    }


    visibleRectDidChange() {

        let el = this.el,
            sEl = ReactDOM.findDOMNode(this._scroller);

        let extension = 100,
            batch = 5,
            height = this.itemHeight(),
            start = Math.max(0, Math.floor(((sEl.scrollTop - extension) / height - batch))),
            end = Math.min(this.content.length,
                Math.ceil((sEl.scrollTop + el.offsetHeight + extension) / height + batch));


        if (start !== this.state.renderRange.start || end !== this.state.renderRange.end) {

            this.setState({
                renderRange: {
                    start: start,
                    end: end
                }
            });
        }
    }


    layout() {

        let start = Math.max(0, this.state.renderRange.start),
            end = Math.min(this.content.length, this.state.renderRange.end),
            i = start,
            vEl;

        for (; i < end; i++) {
            vEl = ReactDOM.findDOMNode(this._itemRefs[i]);
            if (vEl) {
                let top = i * this.itemHeight();
                vEl.style.top = top + "px";
            }
        }

    }
}


export default ProgessiveListView;


