import React from "react";
import ReactDOM from "react-dom";
import EventResponder from "./EventResponder.js";
import isFunction from "lodash/isFunction";


import classNames from "classnames";

import "./view.css";

/**
 *
 *  View
 *  A React Component with an event responder
 *  You can respond to events by implementing
 *
 *  mouseDown()
 *  mouseUp()
 *  mouseMove()
 *  mouseOut()
 *  mouseOver()
 *
 *  keyDown()
 *  keyUp()
 *  keyPress()
 *
 *  onFocus()
 *  onBlur()
 *
 *  onTap()
 *  touchStart()
 *  touchEnd()
 *  touchCancel()
 *  touchMove()
 *
 *  --- States ---
 *  hidden: true | false
 *
 *  --- Props ---
 *
 */
class View extends React.Component {

    constructor(props) {
        super(props);

        this.state = {};
    }


    get isInDocument() {
        return this._isInDocument;
    }

    get className() {
        return classNames(this.props.className, {'v-hidden': this.props.hidden});
    }

    get el() {
        return ReactDOM.findDOMNode(this)
    }

    /**
     *  do not override in subclasses, instead use didEnterDocument
     */

    componentDidMount() {
        EventResponder.init(this, this);
        if (isFunction(this.didEnterDocument)) {
            this.didEnterDocument.call(this);
        }

        this._isInDocument = true;
    }

    /**
     *  do not override in subclasses, instead use didLeaveDocument
     */
    componentWillUnmount() {
        EventResponder.release(this, this);
        if (isFunction(this.didLeaveDocument)) {
            this.didLeaveDocument.call(this);
        }
        this._isInDocument = false;
    }

    render() {
        return (
            <div className={this.className} role={this.props.role}>
                {this.props.children}
            </div>
        )
    }


}


export default View;


