import React from "react";
import ReactDOM from "react-dom";
import isFunction from 'lodash/isFunction';


import EventResponder from "./EventResponder.js";

import "./view.css";

/**
 *
 *  Make a View Draggable
 *
 *  usage:
 *  <Draggable>
 *      ...view to make draggable
 *
 *  </Draggable>
 *
 * --- States ---
 *
 *
 * --- Props ---
 * enabled : true | false - if false, Draggable is not enabled on the View
 * onDrag : function - callback on drag event
 *
 */
class Draggable extends React.Component {

    componentDidMount() {
        if (this.props.enabled)
            EventResponder.init(this, this);
    }

    componentWillUnmount() {
        EventResponder.release(this, this);
    }

    render() {
        return this.props.children;
    }

    isMouseOnHandle(path) {

        if(!this.props.handle)
            return true;

        if(path) {
            let i = 0,
                l = path.length;

            for(; i < l; i++) {

                if(path[i].classList) {
                    if(path[i].classList.contains(this.props.handle)) {
                        return true;
                    }
                }
            }
        }



        return false;
    }

    mouseDown(e) {


        let path = e.path || this._composedPath(e.target);
        let el = ReactDOM.findDOMNode(this);

        if(this.isMouseOnHandle(path) && !el.hasAttribute('resizing')) {

            e.preventDefault();

            this.md = true; //mouse down
            this.dc = 0; //drag count

            el.removeEventListener('mousemove', this.mouseMove, false);
            el.removeEventListener('mouseup', this.mouseUp, false);
            document.addEventListener('mousemove', this.mouseMove, false);
            document.addEventListener('mouseup', this.mouseUp, false);

            this.sx = e.clientX - parseInt(el.style.left, 10); //start x
            this.sy = e.clientY - parseInt(el.style.top, 10); //start y

            this.lz = el.style.zIndex;
            el.style.zIndex = 1000;
        }
    }

    /**
     * polyfill for event.path
     *
     * @param el - event.target
     * @returns {Array}
     * @private
     */
    _composedPath (el) {

        let path = [];

        while (el) {
            path.push(el);
            if (el.tagName === 'HTML') {
                path.push(document);
                path.push(window);
                return path;
            }
            el = el.parentElement;
        }
    }

    mouseUp(e) {

        let el = ReactDOM.findDOMNode(this);

        el.addEventListener('mousemove', this.mouseMove, false);
        document.removeEventListener('mousemove', this.mouseMove, false);

        el.addEventListener('mouseup', this.mouseUp, false);
        document.removeEventListener('mouseup', this.mouseUp, false);

        this.md = false; //mousedown
        this.dc = 0; //drag count
        this.ds = false; //drag started

        el.style.zIndex = this.lz;

        let x = e.clientX - this.sx;
        let y = e.clientY - this.sy;

        if (isFunction(this.props.onDragFinish)) {
            this.props.onDragFinish.call(this, {
                x: x, y: y
            });
        }
    }

    mouseMove(e) {

        if (this.md) {

            let x = e.clientX - this.sx;
            let y = e.clientY - this.sy;

            let el = ReactDOM.findDOMNode(this);

            if (this.props.constrain) {
                x = Math.max(0, Math.min(x, el.parentElement.offsetWidth - el.offsetWidth));
                y = Math.max(0, Math.min(y, el.parentElement.offsetHeight - el.offsetHeight));
            }

            el.style.left = x + "px";
            el.style.top = y + "px";

            this.dc++;

            if (this.dc > 5 && !this.ds) {
                this.ds = true;

                if(isFunction(this.props.onDragStart)) {
                    this.props.onDragStart.call(this, {
                        x: x, y: y
                    });
                }

            }
            else if (this.ds) {
                if (isFunction(this.props.onDrag)) {
                    this.props.onDrag.call(this, {
                        x: x, y: y
                    });
                }
            }
        }
    }


    //TODO: support touch drag
}

Draggable.defaultProps = {enabled: true, constrain: true};

export default Draggable;


