import React from 'react';
import isNil from 'lodash/isNil';
import isFunction from 'lodash/isFunction';
import orderBy from 'lodash/orderBy';
import find from 'lodash/find';
import keyBy from 'lodash/keyBy';
import mapValues from 'lodash/mapValues';

import View from '../View';
import TableViewHeader from "./TableViewHeader";
import TableViewBody from "./TableViewBody";

import classNames from 'classnames';

import './table.css';
import EventResponder from "../EventResponder";


export const ColumnResizePolicy = Object.freeze({
    NONE : 1,
    UNIFORM: 2,
    LAST_COLUMN : 3,
});

const sw = getScrollBarWidth();

/**
 *
 *  display tabular data
 *
 *  --props--
 *  columns
 *  sortInfo
 *  headerHeight
 *  emptyText
 *  data
 *
 */

class TableView extends View {

    constructor(props) {
        super(props);

        let sData = props.data;
        if(props.sortInfo) {
            let column = find(props.columns, {id: props.sortInfo.id});
            if(column){
                let columnId = column.id,
                    sorter = column.sorter,
                    asc = props.sortInfo.asc;

                sData = orderBy(sData, [row => isFunction(sorter) ? sorter(row[columnId]) : row[columnId]], [(asc ? 'asc' : 'desc')])
            }
        }

        Object.assign(this.state, {
            sortInfo: props.sortInfo,
            data: sData,
            scrollLeft:0,
            loading:false,
            columnWidths: mapValues(keyBy(props.columns, 'id'), function(value){
                return (value.width || 100);
            })
        });


        EventResponder.listenFor('resize', this, this._onResize);
        EventResponder.listenFor('resizeFinish', this, this._onResizeFinish);


    }

    componentWillReceiveProps(newProps) {
        if(!isNil(newProps.data)) {
            if(this.state.data !== newProps.data) {
                this.state.data = newProps.data
            }
        }

        if(!isNil(newProps.sortInfo)) {
            if(this.sortInfo !== newProps.sortInfo) {
                this.state.sortInfo = newProps.sortInfo
            }
        }
    }

    render() {

        let tableHeader = null;

        if(this.props.hasHeader) {
            tableHeader =  <TableViewHeader scrollLeft={this.state.scrollLeft}
                                            table={this}
                                            sortInfo={this.state.sortInfo}
                                            headerHeight={this.props.headerHeight}
                                            align={this.props.headerAlign}
                                            ref={e => this._tableHeader = e}/>
        }

        return (
           <div role="grid" className={this.className}>
               {tableHeader}
               <TableViewBody table={this}
                               summary={this.props.summary}
                               emptyText={this.props.emptyText}
                               className={(this.props.hasHeader ? '' : 'noHeader')}
                               data={this.state.data}
                    ref={e => this._tableBody = e}/>
            </div>
       )
    }

    didEnterDocument() {
        setTimeout(function(){

            this.state.lastWidth = this.el.offsetWidth - (this.props.columns.length - 1);

            if(this._tableBody.scrollHeight >  this.el.offsetHeight) {
                this.state.lastWidth-=sw;
            }

            if(this.props.fitColumns) {
                let cWidth = 0;
                this.props.columns.forEach(function(column){
                    cWidth+=(column.width || 100);
                });
                let diff = this.state.lastWidth - cWidth;
                if(diff !== 0) {
                    this.setState({
                        columnWidths: mapValues(this.state.columnWidths, function(c){
                            return Math.round(c+diff*(c/cWidth));
                        })
                    });
                }
            }

        }.bind(this), 0);
    }

    didLeaveDocument() {
        EventResponder.removeListener('resize', this);
        EventResponder.removeListener('resizeFinish', this);
    }

    get className() {
        return classNames('v-TableView', super.className);
    }

    set data(data) {

        if(this.sortInfo) {
            let column = find(this.props.columns, {id: this.sortInfo.id});
            if(column){
                this.sortByColumn({
                    column: column,
                    asc: this.sortInfo.asc
                });

                return;
            }
        }

        this.setState({
            data: data
        });

        setTimeout(function(){this._onResize();}.bind(this), 0);


        if(isFunction(this.didDataChange)) {
            this.didDataChange();
        }
    }

    get data() {
        return this.state.data;
    }

    get sortInfo() {
        return this.state.sortInfo;
    }

    set sortInfo(v) {
        this.setState({
            sortInfo: v
        });
    }

    get columns() {
        return this.props.columns;
    }

    /**
     * Sort table by specified column
     * @param info - config object { column: <-column object-->,
     *              asc: <!-true if sort ascending, false for descending -> }
     *
     */
    sortByColumn(info) {

        if(this.data) {

            let columnId = info.column.id,
                sorter = info.column.sorter,
                asc = info.asc;

            this.setState({
                data: orderBy(this.data, [row => isFunction(sorter) ? sorter(row[columnId]) : row[columnId]], [(asc ? 'asc' : 'desc')]),
                sortInfo: {
                    id: columnId,
                    asc: asc
                }
            });
        }
    }

    _onResize() {

        let columnResizePolicy = this.props.columnResizePolicy,
            columns = this.props.columns,
            w = this.el.offsetWidth - (columns.length - 1);

        if(this._tableBody.scrollHeight >  this.el.offsetHeight) {
            w-=sw;
        }

        if(!this.state.lastWidth) {
            this.state.lastWidth = this.el.offsetWidth - (this.props.columns.length - 1);
        }

        let diff = w - this.state.lastWidth,
            l = columns.length;

        if(l && diff !== 0) {
            if(columnResizePolicy === ColumnResizePolicy.UNIFORM) {
                let dx = Math.floor(diff/l);
                this.state.columnWidths = mapValues(this.state.columnWidths, function(c){
                    return c+dx;
                });
            }
            else if(columnResizePolicy === ColumnResizePolicy.LAST_COLUMN) {
                let lastColumn = columns[l - 1];
                this.state.columnWidths[lastColumn.id]+=diff;
            }

            this.state.lastWidth = w;

            if(this._tableHeader){
                this._tableHeader.forceUpdate();
            }
        }
    }

    _onResizeFinish() {
        if(this._tableBody){
            this._tableBody.forceUpdate();
        }
    }

    _updateColumnWidth(column, finish) {

        if(column) {
            if(this.state.columnWidths[column.id]) {
                this.state.columnWidths[column.id] = column.width;

                if (this._tableHeader) {
                    this._tableHeader.forceUpdate();
                }
            }
        }

        if(finish) {
            if(this._tableBody){
                this._tableBody.forceUpdate();
            }
        }
    }

    _onColumnSort(column) {
        this.sortByColumn({
            column: column,
            asc: isNil(this.state.sortInfo) ? true : !this.state.sortInfo.asc
        });
    }

    _onTableScroll() {
        if(this._tableBody) {
            let left = this._tableBody.el.scrollLeft;
            if(this.state.scrollLeft !== left) {
                this.setState({
                    scrollLeft: left
                });
            }
        }
    }
}

TableView.defaultProps = {
    headerHeight:32,
    hasHeader:true,
    headerAlign:"center",
    emptyText: "No Data",
    columns: [],
    fitColumns:true,
    columnResizePolicy : ColumnResizePolicy.NONE
};


/**
 * computes the width of the scrollbar
 * @returns {number}
 */
function getScrollBarWidth() {

    let inner = document.createElement('p');
    inner.style.width = "100%";
    inner.style.height = "200px";

    let outer = document.createElement('div');
    outer.style.position = "absolute";
    outer.style.top = "0px";
    outer.style.left = "0px";
    outer.style.visibility = "hidden";
    outer.style.width = "200px";
    outer.style.height = "150px";
    outer.style.overflow = "hidden";
    outer.appendChild(inner);

    document.body.appendChild(outer);
    let w1 = inner.offsetWidth;
    outer.style.overflow = 'scroll';
    let w2 = inner.offsetWidth;

    if (w1 === w2) {
        w2 = outer.clientWidth;
    }

    document.body.removeChild(outer);

    return (w1 - w2);
}

export default TableView;


