import React from "react";
import isFunction from "lodash/isFunction";

import startOfMonth from "date-fns/start_of_month";
import startOfWeek from "date-fns/start_of_week";
import startOfDay from "date-fns/start_of_day";
import addDays from "date-fns/add_days";
import addMonths from "date-fns/add_months";
import isSameDay from "date-fns/is_same_day";
import isToday from "date-fns/is_today";

import KeyCodes from '../KeyCodes';
import Control from "../Control";
import ButtonView from "../button/ButtonView";
import SelectView from "../input/SelectView/SelectView";

import classNames from 'classnames';

import './calendar.css';

class DateCell extends Control {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            selected: this.props.selected
        })
    }

    componentWillReceiveProps(newProps) {
         if(newProps.selected !== this.state.selected) {
             this.setState({
                 selected: newProps.selected
             });
         }

         super.componentWillReceiveProps(newProps);
    }

    render() {
        return (
            <td tabIndex={this.state.disabled ? -1 : 0} className={this.className}>{this.props.date.getDate()}</td>
        )
    }


    get className() {
        return classNames('v-Calendar-DayCell',
            {
                'is-selected' : this.state.selected,
                'is-today' : this.props.today
            },

            super.className

            );
    }

    get calendar() {
        return this.props.cal;
    }


    mouseOver() {
        if(this.state.disabled)
            return;

        this.setState({
            hover: true
        });
    }

    mouseOut() {
        this.setState({
            hover: false
        });
    }

    mouseDown(e){
        e.preventDefault();
    }

    doSelect(){

        if(this.calendar.disabled){
            return;
        }

        this.calendar.value = this.props.date;
        if(isFunction(this.props.action)) {
            this.props.action.call(this, this.calendar)
        }
    }

    onClick(e) {
        e.preventDefault();
        this.doSelect();
    }

    keyDown(e) {
        let key = e.which;
        if(key === KeyCodes.KEY_SPACE || key === KeyCodes.KEY_ENTER) {
            this.doSelect();
        }
    }
}


class CalendarView extends Control {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            displayedMonth: this.props.displayedMonth || startOfDay(new Date()),
            selected : this.props.value
        });
    }

    componentWillReceiveProps(newProps) {

        if(newProps.displayedMonth instanceof Date) {
            if(newProps.displayedMonth !== this.displayedMonth) {
                this.displayedMonth = newProps.displayedMonth;
            }
        }

        if(newProps.value instanceof Date) {
            if(newProps.value !== this.value) {
                this.value = newProps.value;
            }
        }

        super.componentWillReceiveProps(newProps);
    }

    renderCurrentMonth() {

        let i = 0,
            weeks = [];

        let month = this.displayedMonth,
            day = startOfWeek(startOfMonth(month));

        for(; i < 6; i++) {

            let week = [],
                j = 0;

            for(; j < 7; j++) {
                week.push(<DateCell cal={this}
                                    selected={isSameDay(day, this.value)}
                                    action={this.props.action}
                                    today={isToday(day)}
                                    disabled={day.getMonth() !== month.getMonth()}
                                    date={day} key={i+j}/>);
                day = addDays(day, 1);
            }

            weeks.push(<tr key={i}>{week}</tr>);

        }

        return (<table className="v-Month">
            <tbody>
            <tr className="v-Calendar-DayLabel">
                <td>Sun</td><td>Mon</td><td>Tue</td><td>Wed</td><td>Thur</td><td>Fri</td><td>Sat</td>
            </tr>
            {weeks}
            </tbody>
        </table>);
    }

    render() {

        let years = [],
            yearCount = this.props.yearCount,
            currentYear = this.displayedMonth.getFullYear(),
            i = currentYear - yearCount/2;

        for(; i < currentYear+yearCount/2; i++) {
            years.push(i);
        }

        return (

            <div className={this.className}>

                <div className="v-Calendar-Controls">

                    <ButtonView action={this.prevMonth.bind(this)}
                        iconOnly={true} icon="fa fa-chevron-left" className="prevMonth" />
                     <SelectView
                         disabled={this.state.disabled}
                         action={this.changeMonth.bind(this)}
                         selectedValue={this.displayedMonth.getMonth()}>
                            <option>January</option>
                            <option>February</option>
                            <option>March</option>
                            <option>April</option>
                            <option>May</option>
                            <option>June</option>
                            <option>July</option>
                            <option>August</option>
                            <option>September</option>
                            <option>October</option>
                            <option>November</option>
                            <option>December</option>
                        </SelectView>

                    <SelectView
                        disabled={this.state.disabled}
                        action={this.changeYear.bind(this)}
                        selectedValue={yearCount/2}>
                        {years.map((year) => {
                            return <option key={year}>{year}</option>
                        })}
                    </SelectView>
                    <ButtonView action={this.nextMonth.bind(this)}
                        iconOnly={true} icon="fa fa-chevron-right" className="nextMonth" />
                </div>
                {this.renderCurrentMonth()}
            </div>
        )
    }

    get className() {
        return classNames('v-Calendar', super.className);
    }

    get displayedMonth() {
        return this.state.displayedMonth;
    }

    set displayedMonth(date) {
        this.setState({
            displayedMonth: date || this.state.displayedMonth
        });
    }

    get value() {
        return this.state.selected;
    }

    set value(date) {
        this.setState({
            selected: date,
            displayedMonth: date || this.state.displayedMonth
        });

    }

    changeMonth(sender) {
        let newMonth = new Date(this.displayedMonth.getTime());
        newMonth.setMonth(sender.selectedIndex);
        this.displayedMonth = newMonth;
    }


    changeYear(sender) {
        let newMonth = new Date(this.displayedMonth.getTime());
        newMonth.setFullYear(parseInt(sender.value, 10));
        this.displayedMonth = newMonth;
    }

    nextMonth() {
        let newMonth = new Date(this.displayedMonth.getTime());
        newMonth = addMonths(newMonth, 1);
        this.displayedMonth = newMonth;
    }

    prevMonth() {
        let newMonth = new Date(this.displayedMonth.getTime());
        newMonth = addMonths(newMonth, -1);
        this.displayedMonth = newMonth;
    }

}


CalendarView.defaultProps = {yearCount : 40};



export default CalendarView;


