import React from "react";

import TextView from "./TextView";
import ButtonView from "../../button/ButtonView";

import classNames from "classnames";


class SearchTextView extends TextView {

    constructor(props) {
        super(props);
        this.cancel = this.cancel.bind(this);
    }

    get className() {
        return classNames('v-SearchText', super.className);
    }

    renderControl() {

        if (this.props.multiline) {
            throw new Error('SearchTextView does not support multiline.');
        }

        return super.renderControl();

    }

    render() {
        return (
            <div className={this.className} role="search" aria-disabled={this.state.disabled}>
                <i className="icon fa fa-search"/>
                {this.renderControl()}
                <ButtonView action={this.cancel}
                            hidden={this.value ? this.value.length === 0 : true}
                            icon="fa fa-times-circle" iconOnly={true}
                            className="v-SearchText-reset"/>
            </div>
        )
    }

    cancel() {
        this.value = "";
        this._inputChanged();

    }

    _inputChanged() {
        if (super._inputChanged()) {
            this.forceUpdate();
        }
    }


}

SearchTextView.defaultProps = {
    placeholder: 'Search'
};

export default SearchTextView;


