import React from 'react';
import isFunction from 'lodash/isFunction';
import isNil from 'lodash/isNil';

import View from '../View';

import classNames from 'classnames';

import './table.css';

class TableColumnResize extends View {

    render(){
        return (
            <div className="v-cResize"/>
        )
    }


    onClick(e) {
        e.stopPropagation();
    }

    mouseDown(e) {
        e.preventDefault();
        this._md = true;

        document.addEventListener('mouseup', this.mouseUp, false);
        document.addEventListener('mousemove', this.mouseMove, false);
        document.body.style.cursor = 'ew-resize';
    }

    mouseUp(){
        this._md = false;

        document.removeEventListener('mouseup', this.mouseUp, false);
        document.removeEventListener('mousemove', this.mouseMove, false);
        document.body.style.cursor = 'default';

        if(isFunction(this.props.resize)) {
            this.props.resize(null, true);
        }

    }

    mouseMove(e) {

        if(this._md) {
            let el = this.el;
            if(el) {
                let parent = el.parentNode,
                    rect = parent.getBoundingClientRect();

                if(isFunction(this.props.resize)) {
                    this.props.resize({
                        id: this.props.column.id,
                        width: e.clientX - rect.left
                    });
                }
            }
        }
    }
}

class TableColumnHeaderView extends View {

    render() {
        let column = this.props.column;
        let resizer = null;
        if(column.resizable !== false){
            resizer = <TableColumnResize
                resize={this.props.resize}
                header={this.props.header}
                column={column}/>
        }

        let sortAscIndicator = null;
        if(column.sortable && this.props.sortAsc === true) {
            sortAscIndicator = <div className="v-sortAsc"/>
        }

        let sortDescIndicator = null;
        if(column.sortable && this.props.sortAsc === false) {
            sortDescIndicator = <div className="v-sortDesc"/>
        }

        return (
            <div style={{width: (this.props.width || 100)}}
                 className="v-TableColumnHeader">
                {sortAscIndicator}
                <div style={{textAlign: this.props.align}} className="title">{column.name}</div>
                {resizer}
                {sortDescIndicator}
            </div>
        )
    }

    onClick(e) {
        let column = this.props.column;
        if(column.sortable) {
            if(isFunction(this.props.onSort)) {
                if(isNil(column.sortAsc) || !column.sortAsc) {
                    column.sortAsc = true;
                }
                else {
                    column.sortAsc = false;
                }
                this.props.onSort(this.props.column);
            }
        }
    }

    mouseDown(e){
        e.preventDefault();
    }

}


class TableViewHeader extends View {

    constructor(props) {
        super(props);
        Object.assign(this.state, {
            columns: props.columns || []
        });
    }

    componentWillReceiveProps(newProps) {

        if(!isNil(newProps.columns)) {
            if(this.state.columns !== newProps.columns) {
                this.state.columns = newProps.columns
            }
        }
    }

    render() {

        let table = this.props.table;
        return (
            <div className={this.className}
                 style={{
                            height: this.props.headerHeight,
                            left:(-1*this.props.scrollLeft)
                        }}>
                {table.columns.map(function(column, index){
                    return <TableColumnHeaderView
                        resize={table._updateColumnWidth.bind(table)}
                        onSort={table._onColumnSort.bind(table)}
                        sortAsc={isNil(this.props.sortInfo) ? null : (
                            column.id === this.props.sortInfo.id ? this.props.sortInfo.asc :
                            null)}
                        header={this}
                        column={column}
                        width={table.state.columnWidths[column.id] || 100}
                        align={this.props.align}
                        key={'c'+index}/>
                }.bind(this))}

            </div>
        )
    }

    get className() {
        return classNames('v-TableViewHeader', super.className);
    }
}


export default TableViewHeader;


