import View from "./View.js";

import classNames from "classnames";

/**
 *
 *  Control - generic UI control with active, disabled, focused, and hover states
 *
 */

class Control extends View {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            disabled: this.props.disabled || false,
            active: false,
            hover: false,
            focused: false
        });
    }

    didEnterDocument() {
        this.componentDidUpdate();
    }

    componentDidUpdate() {
        if(this.focused){
            setTimeout(function(){this.el.focus();}.bind(this), 100);
        }
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.disabled !== this.state.disabled) {
            this.state.disabled = nextProps.disabled;
        }
    }

    get disabled() {
        return this.state.disabled;
    }

    set disabled(flag) {
        this.setState({
            disabled: flag
        });
    }

    set focused(flag) {
        this.setState({
            focused: flag
        });
    }

    get focused() {
        return this.state.focused;
    }

    get className() {
        return classNames({
            'is-disabled': this.state.disabled,
            'is-focused': this.state.focused,
            'is-hover': this.state.hover,
            'is-active': this.state.active
        }, super.className);
    }

}


export default Control;


