import React from "react";
import isFunction from "lodash/isFunction";

import View from "../View";
import ScrollView from "../ScrollView";

class ListView extends View {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            content: this.props.content || [],
            renderRange: {start: 0, end: Number.MAX_SAFE_INTEGER}
        });

        if (!isFunction(this.renderItem)) {
            throw new Error("Make sure to implement 'renderItem(model)' on the list")
        }
    }

    componentWillReceiveProps(newProps) {

        if(newProps.content && this.state.content !== newProps.content) {
            this.state.content = newProps.content;

            if(isFunction(this.props.contentDidChange)){
                this.props.contentDidChange.call(this);
            }
        }

    }


    get content() {
        return this.state.content;
    }

    set content(arr) {
        if(arr && this.state.content !== arr) {

            this.setState({
                content: arr
            });

            if(isFunction(this.props.contentDidChange)){
                this.props.contentDidChange.call(this);
            }
        }

    }

    render() {

        let start = Math.max(0, this.state.renderRange.start),
            end = Math.min(this.state.content.length, this.state.renderRange.end),
            i = start,
            item;

        let viewsToInsert = [];

        for (; i < end; i++) {
            item = this.state.content[i];
            viewsToInsert.push({view: this.renderItem(item, i), index: i});
        }

        this._itemRefs = {};

        return (


            <div className={this.className}>
                <ScrollView ref={(e) => this._scroller = e}>
                    <div ref={(e) => this._content = e}>
                        {viewsToInsert.map((item, index) => {
                            return React.cloneElement(item.view, {
                                key: index,
                                ref: (e) => this._itemRefs[start + index] = e
                            })
                        })}
                    </div>
                </ScrollView>
            </div>
        )
    }

}


export default ListView;


