import React from "react";

import EventResponder from "./EventResponder";
import View from "./View";

import "./view.css";


/**
 *  RootView
 *
 *
 */
class RootView extends View {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            tabIndex: -1
        });

        EventResponder.listenFor('modalOn', this, this.onModal);
        EventResponder.listenFor('modalOff', this, this.offModal);
    }

    render() {
        return (
            <div id="v-Root" tabIndex={this.state.tabIndex}>
                {this.props.children}
            </div>
        )
    }

    didEnterDocument() {
        window.addEventListener('resize', this.onResize, false);
    }

    didLeaveDocument() {
        window.removeEventListener('resize', this.onResize, false);
        EventResponder.removeListener('modalOn', this);
        EventResponder.removeListener('modalOff', this);
    }

    onResize() {
        EventResponder.postNotification('resize');

        if(this._resizeTimer) {
            clearTimeout(this._resizeTimer);
        }
        this._resizeTimer = setTimeout(function(){
            EventResponder.postNotification('resizeFinish');
        }, 300);
    }

    onModal() {
        this.setState({
            tabIndex:0
        });
    }

    offModal() {
        this.setState({
            tabIndex:-1
        });
    }

    onFocus(e) {
        let modals = document.getElementsByClassName('v-WindowPanel-modalMask');
        if(modals.length) {
            e.preventDefault();
            let firstFocus = modals[0].getElementsByClassName('v-WindowPanel-close');
            if(firstFocus.length){
                firstFocus[0].focus();
            }
        }
    }

}

export default RootView;


