import React from "react";
import ReactDOM from "react-dom";
import isFunction from "lodash/isFunction";

import View from "../View"
import EventResponder from "../EventResponder";
import ButtonView from "../button/ButtonView";
import MenuButtonView from "../menu/MenuButtonView";
import MenuView from "../menu/MenuView";
import MenuItemView from "../menu/MenuItemView";

import classNames from "classnames";

import "./toolbar.css";

class ToolbarView extends View {

    constructor(props) {
        super(props);

        this._doMeasure = this._doMeasure.bind(this);
        this.onResize = this.onResize.bind(this);

        Object.assign(this.state, {
            moreItems: []
        });


        EventResponder.listenFor("resize", this, this.onResize);
    }

    get className() {
        return classNames('v-Toolbar', super.className);
    }

    render() {

        this._rItems = [];
        this._lItems = [];

        let rIndex = 0, lIndex = 0;

        return (
            <div className={this.className} role="toolbar">
                <div className="v-Toolbar-section v-Toolbar-section--left">
                    {React.Children.map(this.props.children, (item) => {
                        let section = item.props.section;
                        if (!section || section === "left") {
                            return React.cloneElement(item, {
                                ref: (node) => {
                                    // Keep your own reference
                                    this._lItems.push(node);
                                    // Call the original ref, if any
                                    const {ref} = item;
                                    if (isFunction(ref)) {
                                        ref(node);
                                    }
                                },
                                key: lIndex++
                            });
                        }
                    })}
                    <MenuButtonView hidden={this.state.moreItems.length === 0} text="More" ref={(e) => this._overflow = e}>
                        <MenuView>{this.state.moreItems}</MenuView>
                    </MenuButtonView>
                </div>

                <div className="v-Toolbar-section v-Toolbar-section--right">
                    {React.Children.map(this.props.children, (item) => {
                        let section = item.props.section;
                        if (section === "right") {
                            return React.cloneElement(item, {
                                ref: (node) => {
                                    // Keep your own reference
                                    this._rItems.push(node);
                                    // Call the original ref, if any
                                    const {ref} = item;
                                    if (isFunction(ref)) {
                                        ref(node);
                                    }
                                },
                                key: rIndex++
                            });
                        }
                        return null;
                    })}
                </div>

            </div>
        );
    }

    didEnterDocument() {
        this._doMeasure();
    }

    didLeaveDocument() {
        EventResponder.removeListener('resize', this);
    }


    onResize() {

        if(this._resizeTimer) {
            clearTimeout(this._resizeTimer);
        }
        else {
            this._doMeasure();
        }

        this._resizeTimer = setTimeout(function () {
            this._doMeasure();
        }.bind(this), 50);

    }


    _postMeasure(pxWidth) {

        if (pxWidth < 0) {

            pxWidth -= 75;

            let leftCount = this._lItems.length,
                l = leftCount;

            while (pxWidth < 0 && l > -1) {
                l--;

                if (l > -1) {
                    let el = ReactDOM.findDOMNode(this._lItems[l]);
                    pxWidth += (el.offsetWidth);
                }
            }

            if (leftCount - l < 2) {
                l--;
            }

            if (l < 0) {
                l = 0;
            }

            let i = l;

            let overflow = this._overflow;

            if (i < leftCount - 1) {
                ReactDOM.findDOMNode(overflow).classList.remove('hidden');
            }

            let menuItems = [];

            for (; i < leftCount; i++) {
                let v = this._lItems[i];
                if (v instanceof ButtonView) {
                    menuItems.push(<MenuItemView {...v.props} />);
                }

                ReactDOM.findDOMNode(v).classList.add('hidden');
            }

            this.setState({
                moreItems: menuItems
            });

        }
    }


    _preMeasure() {

        let pxWidth = this.el.offsetWidth;

        pxWidth -= 16;

        let rightCount = this._rItems.length,
            r = rightCount,
            rWidth = 0,
            i = 0;

        for (; i < r; i++) {
            if (this._rItems[i])
                rWidth += ReactDOM.findDOMNode(this._rItems[i]).offsetWidth;
        }

        pxWidth -= rWidth;

        let l = this._lItems.length,
            lWidth = 0;

        i = 0;


        for (; i < l; i++) {
            let v = this._lItems[i],
                vEl = ReactDOM.findDOMNode(v);

            if (vEl) {
                vEl.classList.remove('hidden');
                lWidth += vEl.offsetWidth;
            }
        }

        pxWidth -= lWidth;


        return pxWidth;
    }


    _doMeasure() {

        if (this.isInDocument) {
            let overflow = this._overflow,
                oEl = ReactDOM.findDOMNode(overflow);

            if (oEl) {
                oEl.classList.add('hidden');

                let m = this._preMeasure();

                if (m < 0) {
                    this._postMeasure(m);
                    if (overflow.itemCount > 0) {
                        oEl.classList.remove('hidden');
                    }
                }
            }
        }


    }
}


export default ToolbarView;


