import React from "react";
import ReactDOM from "react-dom";
import isFunction from 'lodash/isFunction';

import WindowPanel from '../window/WindowPanel';
import ButtonView from '../button/ButtonView';
import TextView from '../input/TextView/TextView';

import classNames from 'classnames';

import './messagebox.css';


class MessageBox extends WindowPanel {

    renderPrompt() {

        return (
            <div>
                <div className="icon fa fa-question-circle"></div>
                <div className="message"  ref={(e) => this._msg = e}>{this.props.message}
                    <TextView ref={(e)=>this._input = e} />
                </div>
                <ButtonView text="Cancel" className="cancel" action={this.onCancel.bind(this)} />
                <ButtonView text="OK" className="confirm" type="primary" action={this.onConfirm.bind(this)} />
            </div>
        )

    }

    renderConfirm() {
        return (
            <div>
                <div className="icon fa fa-question-circle"></div>
                <div className="message" ref={(e) => this._msg = e}>{this.props.message}</div>
                <ButtonView text="Cancel" className="cancel" action={this.onCancel.bind(this)} />
                <ButtonView text="OK" className="confirm" type="primary" action={this.onConfirm.bind(this)} />
            </div>
        )
    }

    renderAlert() {
        return (
            <div>
                <div className="icon fa fa-exclamation-triangle"></div>
                <div className="message" ref={(e) => this._msg = e}>{this.props.message}</div>
                <ButtonView text="OK" className="confirm" type="primary" action={this.onConfirm.bind(this)} />
            </div>
        )
    }

    renderContent() {

        let intent = this.props.intent;
        switch (intent) {
            case 'alert':
                return this.renderAlert();
                break;
            case 'confirm':
                return this.renderConfirm();
                break;
            case 'prompt':
                return this.renderPrompt();
                break;
        }

    }

    get className() {
        return classNames('v-MessageBox', super.className);
    }

    didEnterDocument() {
        super.didEnterDocument();

        setTimeout(function(){
            this.dropIn();
            if(this._input) {
                this._input.focused = true;
            }

            this._msg.style.top = (((this.props.intent==='prompt' ? 180 : 200) - this._msg.offsetHeight)/2.0) + 'px';

        }.bind(this), 100);
    }

    onConfirm() {
        if(isFunction(this.props.callback)) {
            this.props.callback.call(this, this._input ? this._input.value : true);
        }
        this.close();
    }

    onCancel() {
        if(isFunction(this.props.callback)) {
            this.props.callback.call(this, false);
        }
        this.close();
    }

    static alert(props, callback) {
        ReactDOM.render(<MessageBox modal={true}
                                    intent="alert"
                                    callback={callback}
                                    width={520}
                                    height={270}
                                    resizable={false}
                                    title={props.title}
                                    message={props.message}/>,
            document.createElement('div'));
    }


    static confirm(props, callback) {

        ReactDOM.render(<MessageBox modal={true}
                                    intent="confirm"
                                    callback={callback}
                                    width={520}
                                    height={270}
                                    resizable={false}
                                    title={props.title}
                                    message={props.message}/>,
            document.createElement('div'));

    }


    static prompt(props, callback) {

        ReactDOM.render(<MessageBox modal={true}
                                    intent="prompt"
                                    callback={callback}
                                    width={520}
                                    height={270}
                                    resizable={false}
                                    title={props.title}
                                    message={props.message}/>,
            document.createElement('div'));

    }





}




export default MessageBox;



