import React from "react";
import uniqueId from "lodash/uniqueId";
import isFunction from "lodash/isFunction";
import isNil from "lodash/isNil";

import InputControl from "../InputControl";

import classNames from "classnames";

import "./checkbox.css";

/**
 *
 *  Checkbox - on or off input
 *
 *  --- Props ---
 *  name
 *  text
 *  tabIndex
 *  action
 *
 *
 */


class CheckboxView extends InputControl {

    componentWillReceiveProps(newProps) {

        if(!isNil(newProps.checked)) {
            if(newProps.checked !== this.value) {
                this.value = newProps.checked;
            }
        }
        super.componentWillReceiveProps(newProps);
    }


    get className() {
        return classNames('v-Checkbox', super.className);
    }

    get value() {
        return this._input.checked;
    }

    set value(v) {
        this._input.checked = v;
    }

    render() {

        let id = uniqueId('input-');
        return (
            <div className={this.className}
                 aria-checked={this.state.checked}
                 aria-disabled={this.state.disabled}
                 role="checkbox">
                <input type='checkbox' ref={(e) => this._input = e}
                       id={id}
                       onChange={this._inputChanged.bind(this)}
                       name={this.props.name}
                       tabIndex={this.state.disabled ? '-1' : this.props.tabIndex}
                />
                <div className="indicator"/>
                <label className="label v-no-select" htmlFor={id}>{this.props.text}</label>
            </div>
        )

    }

    _inputChanged() {
        this.setState({
            checked: this.value
        });

        if (super._inputChanged()) {
            if (isFunction(this.props.action)) {
                this.props.action.call(this, this);
            }

            return true;
        }

        return false;
    }

    mouseDown(e) {
        e.preventDefault();
    }

}

CheckboxView.defaultProps = {tabIndex: 0};

export default CheckboxView;


