import React from "react";
import isFunction from "lodash/isFunction";

import KeyCodes from "../KeyCodes";
import Control from "../Control";
import PopOverView, {Edge, Position} from "../popover/PopOverView";

import classNames from "classnames";

import "./button.css";

/**
 *  button
 *
 *  --- States ---
 *  disabled
 *
 *
 *  --- Props ---
 *  icon
 *  iconOnly
 *  text
 *  action
 *  type - default, primary, danger, info, inverse, success
 *
 */

class ButtonView extends Control {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            ttposition: Position.TOP
        });
    }

    get className() {
        return classNames('v-Button', this.props.type, {
            'v-Button--hasIcon': this.props.icon,
            'v-Button--iconOnly': this.props.iconOnly
        }, super.className);
    }

    render() {

        if (this.props.tooltip) {
            return (
                <button tabIndex={this.state.disabled ? '-1' : this.props.tabIndex}
                        aria-disabled={this.state.disabled}
                        className={this.className}>
                    <div className='icon'>
                         <i className={this.props.icon}/>
                    </div>
                    <span className='label'>{this.props.text}</span>
                    <PopOverView ref={(e) => this._ttp = e}
                                 backgroundColor="#fafafa"
                                 borderColor="#fafafa"
                                 edge={Edge.CENTER} position={this.state.ttposition}
                                 className="v-Tooltip">
                        {this.props.tooltip}
                    </PopOverView>
                </button>
            )
        }
        else {
            return (
                <button tabIndex={this.state.disabled ? '-1' : this.props.tabIndex}
                        className={this.className}>
                    <i className={classNames('icon', this.props.icon)}/>
                    <span className='label'>{this.props.text}</span>
                </button>
            )
        }
    }

    mouseDown(e) {

        e.preventDefault();

        if (this.state.disabled)
            return;

        if (e.button === 0) {

            //capture the mouse up
            this.el.removeEventListener('mouseup', this.mouseUp);
            document.addEventListener('mouseup', this.mouseUp, false);

            this.setState({
                active: true
            });
        }
    }

    mouseUp() {

        //release the mouse up
        this.el.addEventListener('mouseup', this.mouseUp);
        document.removeEventListener('mouseup', this.mouseUp, false);

        this.setState({
            active: false
        });
    }

    mouseOver(e) {

        this.setState({
            hover: true
        });

        clearTimeout(this.ctt);

        if (this.props.tooltip) {

            let rect = this.el.getBoundingClientRect(),
                offsetY = rect.top;

            this.stt = setTimeout(function () {

                let position = Position.TOP;
                if (offsetY - 28 < 0) {
                    position = Position.BOTTOM;
                }

                this.setState({
                    ttposition: position
                });

                if (this._ttp) {
                    this._ttp.show(this);
                }
            }.bind(this), 1000);
        }

    }

    mouseOut() {
        this.setState({
            hover: false
        });

        if (this.props.tooltip) {
            clearTimeout(this.stt);

            this.ctt = setTimeout(function () {
                if (this._ttp) {
                    this._ttp.close();
                }
            }.bind(this), 500);
        }
    }

    onClick(e) {

        e.stopPropagation();

        if (this.state.disabled)
            return;

        clearTimeout(this.stt);

        if (isFunction(this.props.action))
            this.props.action.call(this, this);
    }

    keyDown(e) {

        if (this.state.disabled)
            return;

        let keyCode = e.which;
        this.setState({
            active: keyCode === KeyCodes.KEY_ENTER || keyCode === KeyCodes.KEY_SPACE
        });
    }

    keyUp() {
        this.setState({
            active: false
        });
    }

    touchStart(e) {

        if (this.state.disabled)
            return;

        e.stopPropagation();
        e.preventDefault();

        this.setState({
            active: true
        });

    }

    touchMove(e) {
        e.preventDefault();
        e.stopPropagation();
    }

    touchCancel(e) {
        e.preventDefault();
        e.stopPropagation();

        this.setState({
            isActive: false
        });
    }


    touchEnd(e) {
        e.preventDefault();
        e.stopPropagation();

        this.setState({
            isActive: false
        });
    }

    onTap(e) {

        e.stopPropagation();

        if (this.state.disabled)
            return;

        clearTimeout(this.stt);

        if (isFunction(this.props.action))
            this.props.action.call(this, this);
    }
}

ButtonView.defaultProps = {type: 'default', tabIndex: 0};

export default ButtonView;


