import React from "react";
import ReactDOM from "react-dom";
import isFunction from "lodash/isFunction";

import View from "../View.js";
import MenuItemView from "./MenuItemView";
import PopOverView from "../popover/PopOverView";
import KeyCodes from "../KeyCodes";

import classNames from "classnames";

import "./menu.css";

/**
 *
 *  MenuView
 *
 *
 *  --- States ---
 *
 *  open
 *
 *  --- Props ---
 *  onClose
 *  onOpen
 *
 *
 */


class MenuPopOverView extends PopOverView {

    mouseMove() {
        MenuItemView.disableMouseOver = false;
    }

    keyDown(e) {

        let menu = this.props.menu,
            active = menu.activeItem,
            keyCode = e.which,
            index;

        e.preventDefault();

        if (!active) {
            index = -1;
        }
        else {
            index = active.props.index;
        }

        let next = null;

        switch (keyCode) {
            case KeyCodes.KEY_DOWN:
                index++;
                next = this.nextMenuItemAt(index);
                break;
            case KeyCodes.KEY_UP:
                index--;
                next = this.prevMenuItemAt(index);
                break;
            case KeyCodes.KEY_RIGHT:
                let submenu = active.submenu;
                if (submenu) {
                    let item = submenu.itemAtIndex(0);
                    ReactDOM.findDOMNode(submenu).focus();
                    item.setState({active: true});
                    return;
                }
                break;
            case KeyCodes.KEY_LEFT:
                if (menu.superMenu) {
                    menu.hide();
                    ReactDOM.findDOMNode(menu.superMenu).focus();
                    return;
                }
                break;
            case KeyCodes.KEY_ENTER:
                if (active !== null) {
                    active.select();
                    return;
                }
                break;
            case KeyCodes.KEY_ESCAPE:
                menu.close();
                return;
            default: {
            }
        }

        if (next) {
            if (active)
                active.setState({active: false});

            next.setState({active: true});
            menu.activeItem = next;

        }
    }

    nextMenuItemAt(index) {

        let menu = this.props.menu,
            item = menu.itemAtIndex(index),
            l = menu.itemCount;

        while (!(item instanceof MenuItemView) && index < l) {
            index++;
            item = menu.itemAtIndex(index);
        }

        return item;
    }

    prevMenuItemAt(index) {
        let menu = this.props.menu,
            item = menu.itemAtIndex(index);

        while (!(item instanceof MenuItemView) && index > -1) {
            index--;
            item = menu.itemAtIndex(index);
        }

        return item;
    }
}


class MenuView extends View {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            open: false
        });

        this.onClose = this.onClose.bind(this);
        this.onOpen = this.onOpen.bind(this);

    }

    get className() {
        return classNames('v-Menu', super.className);
    }

    get itemCount() {
        return React.Children.count(this.props.children);
    }

    get activeItem() {
        return this._activeItem;
    }

    set activeItem(mi) {
        this._activeItem = mi;
    }

    render() {

        this._itemRefs = {};

        return (
            <MenuPopOverView onClose={this.onClose} onOpen={this.onOpen}
                             ref={(e) => this._popover = e} callout={false} tabIndex="0" role="menu"
                             className="v-Menu-popover" backgroundColor="#fff" menu={this}>
                <ul className={this.className}>
                    {React.Children.map(this.props.children, (menuItem, index) => {
                        return React.cloneElement(menuItem, {
                            ref: (node) => {
                                // Keep your own reference
                                this._itemRefs[index] = node;
                                // Call the original ref, if any
                                const {ref} = menuItem;
                                if (isFunction(ref)) {
                                    ref(node);
                                }
                            },
                            key: index,
                            index: index,
                            menu: this
                        });
                    })}
                </ul>
            </MenuPopOverView>
        )
    }

    focus() {
        this.el.focus();
    }

    itemAtIndex(index) {
        return this._itemRefs[index];
    }

    show(x, y) {

        if (this.activeItem) {
            this.activeItem.setState({'active': false});
        }

        this.activeItem = null;

        this.setState({'open': true});
        this._popover.open();

        this.el.style.left = x + 'px';
        this.el.style.top = y + 'px';

    }

    hide() {
        this.setState({'open': false});
        this._popover.close();
    }

    close() {
        this.hide();
        if (this.superMenu) {
            this.superMenu.close();
        }
    }

    onOpen() {

        this.setState({'open': true});
        if (isFunction(this.props.onOpen)) {
            this.props.onOpen.call(this);
        }
    }

    onClose() {

        this.setState({'open': false});
        if (isFunction(this.props.onClose)) {
            this.props.onClose.call(this);
        }
    }

}


export default MenuView;


