import React from "react";
import isString from "lodash/isString";

import KeyCodes from "../../KeyCodes";
import InputControl from "../InputControl";

import classNames from "classnames";

import "./textview.css";

/**
 *  TextView - text control input
 *
 * --- States ---
 * disabled
 * invalid
 *
 *
 * --- Props ---
 * name
 * placeholder
 * multiline
 * readOnly
 * secure
 *
 */
class TextView extends InputControl {


    constructor(props) {
        super(props);
        if (this.textDidChange)
            this.textDidChange = this.textDidChange.bind(this);
    }

    componentWillReceiveProps(newProps) {

        if(isString(newProps.value)) {
            if(newProps.value !== this.value) {
                this.value = newProps.value;
            }
        }

        super.componentWillReceiveProps(newProps);
    }

    get className() {
        return classNames('v-Text', {
            'multiline': this.props.multiline
        }, super.className);
    }

    renderControl() {

        if (this.props.multiline) {
            return ( <textarea rows='1' ref={(e) => this._input = e}
                               tabIndex={this.state.disabled ? '-1' : this.props.tabIndex}
                               disabled={this.state.disabled}
                               readOnly={this.props.readOnly}
                               onChange={this._inputChanged.bind(this)}
                               name={this.props.name}
                               placeholder={this.props.placeholder}
                               className='v-Text-input'/>
            );
        }
        else {
            return (
                <input type={this.props.secure ? 'password' : 'text'} ref={(e) => this._input = e}
                       tabIndex={this.state.disabled ? '-1' : this.props.tabIndex}
                       disabled={this.state.disabled}
                       readOnly={this.props.readOnly}
                       name={this.props.name}
                       onChange={this._inputChanged.bind(this)}
                       placeholder={this.props.placeholder}
                       className='v-Text-input'/>
            );
        }


    }

    render() {
        return (
            <div className={this.className} role="textbox" aria-disabled={this.state.disabled}>
                {this.renderControl()}
                {this.props.children}
            </div>
        )
    }

    didEnterDocument() {

        setTimeout(function () {
            if (this.props.value) {
                this.value = this.props.value;
            }
            else {
                this.value = "";
            }
        }.bind(this), 10);

        super.didEnterDocument();

    }

    _inputChanged() {
        if (super._inputChanged()) {
            if (this.props.textDidChange) {
                this.props.textDidChange.call(this, this);
            }
            return true;
        }

        return false;
    }


    selectionStart() {
        return this._input.selectionStart;
    }

    selectionEnd() {
        return this._input.selectionEnd;
    }

    setSelectionRange(begin, end) {
        return this._input.setSelectionRange(begin, end);
    }

    keyPress(e) {
        if (e.which === KeyCodes.KEY_ENTER) {
            if (!this.props.multiline) {
                e.preventDefault();
                if (this.props.action) {
                    this.props.action.call(this, this);
                }
            }
        }
    }


}

TextView.defaultProps = {
    readOnly: false
};

export default TextView;


