import React from "react";
import ReactDOM from "react-dom";
import isFunction from 'lodash/isFunction';

import EventResponder from "./EventResponder.js";


import "./view.css";

/**
 *
 *  Make a View resizable
 *
 *  usage:
 *  <Resizable>
 *      ...view to make resizable
 *
 *  </Resizable>
 *
 * --- States ---
 *
 *
 *
 * --- Props ---
 * enabled : true | false - if false, Resizable is not enabled on the View
 * onResize : function - callback on resize event
 * onResizeFinish : function - callback on resize finish
 *
 */
class Resizable extends React.Component {

    componentDidMount() {
        if (this.props.enabled)
            EventResponder.init(this, this);
    }

    render() {
        return this.props.children;
    }

    componentWillUnmount() {
        EventResponder.release(this, this);
    }

    mouseMove(e) {

        let el = ReactDOM.findDOMNode(this),
            rect = el.getBoundingClientRect();

        let x = e.clientX - rect.left,
            y = e.clientY - rect.top;

        el.style.cursor = this.getCursor(x, y, rect).style;

        if (this.md && this.cur) {
            let style = this.cur.style,
                min = this.props.minSize,
                max = this.props.maxSize;
            if (style === 'nwse-resize') {
                el.style.width = Math.max(min.width,
                    Math.min(max.width, x)) + 'px';
                el.style.height = Math.max(min.height,
                    Math.min(max.height, y)) + 'px';

            }
            else if (style === 'nesw-resize') {

                let w = this.rx - rect.left - x,
                    l = rect.left + x;

                if (w < max.width && w > min.width) {
                    el.style.left = l + 'px';
                    el.style.width = (this.rx - l) + 'px';
                }

                el.style.height = Math.max(min.height,
                    Math.min(max.height, y)) + 'px';
            }
            else if (style === 'ew-resize' && this.cur.dir === 'left') {
                let w = this.rx - rect.left - x,
                    l = rect.left + x;

                if (w < max.width && w > min.width) {
                    el.style.left = l + 'px';
                    el.style.width = (this.rx - l) + 'px';
                }
            }
            else if (style === 'ew-resize' && this.cur.dir === 'right') {
                el.style.width = Math.max(min.width,
                    Math.min(max.width, x)) + 'px';

            }
            else if (style === 'ns-resize') {
                el.style.height = Math.max(min.height,
                    Math.min(max.height, y)) + 'px';
            }

            el.style.cursor = style;
            document.body.style.cursor = style;

            if (isFunction(this.props.onResize) && this._isResizing ) {
                this.props.onResize.call(undefined, {
                    width: el.offsetWidth,
                    height: el.offsetHeight
                });
            }
        }

    }

    mouseDown(e) {

        let el = ReactDOM.findDOMNode(this),
            rect = el.getBoundingClientRect();

        let x = e.clientX - rect.left,
            y = e.clientY - rect.top;

        this.md = true;
        this.cur = this.getCursor(x, y, rect);
        this.rx = rect.left + rect.width;

        if (this.cur.style !== 'default') {
            e.stopPropagation();
            e.preventDefault();
            el.removeEventListener('mousemove', this.mouseMove, false);
            document.addEventListener('mousemove', this.mouseMove, false);
            this._isResizing = true;
            el.setAttribute('resizing', true);
        }

        document.addEventListener('mouseup', this.mouseUp, false);

    }

    mouseUp() {

        this.md = false;
        this.cur = null;
        document.body.style.cursor = 'default';

        let el = ReactDOM.findDOMNode(this);
        el.addEventListener('mousemove', this.mouseMove, false);
        document.removeEventListener('mousemove', this.mouseMove, false);
        document.removeEventListener('mouseup', this.mouseUp, false);

        if (isFunction(this.props.onResizeFinish) && this._isResizing) {
            this.props.onResizeFinish.call(undefined, {
                width: el.offsetWidth,
                height: el.offsetHeight
            });
        }

        this._isResizing = false;
        el.removeAttribute('resizing');

    }

    getCursor(x, y, rect) {

        if (x < 10 && y > rect.height - 10) {
            return {
                style: 'nesw-resize'
            }
        }
        else if (x > rect.width - 10 && y > rect.height - 10) {
            return {
                style: 'nwse-resize'
            }
        }
        else if (x < 5) {
            return {
                style: 'ew-resize',
                dir: 'left'
            }
        }
        else if (x > rect.width - 5) {
            return {
                style: 'ew-resize',
                dir: 'right'
            }
        }
        else if (y > rect.height - 5) {
            return {
                style: 'ns-resize'
            }
        }

        return {
            style: 'default'
        }
    }

}

Resizable.defaultProps = {
    minSize: {width: 100, height: 100},
    maxSize: {width: 10000, height: 10000},
    enabled: true
};


export default Resizable;


