import Control from "../Control.js";


class InputControl extends Control {
    constructor(props) {
        super(props);

        this._inputFocused = this._inputFocused.bind(this);
        this._inputBlurred = this._inputBlurred.bind(this);
    }

    get value() {
        if (this._input)
            return this._input.value;
    }

    set value(v) {
        if (this._input)
            this._input.value = v;
    }

    _inputFocused() {
        this.setState({
            focused: true
        });

        if (this.onFocus) {
            this.onFocus.call(this);
        }
    }

    _inputBlurred() {
        this.setState({
            focused: false
        });

        if (this.onBlur) {
            this.onBlur.call(this);
        }
    }

    didEnterDocument() {

        if (this._input) {
            this._input.addEventListener('focus', this._inputFocused);
            this._input.addEventListener('blur', this._inputBlurred);
        }

        super.didEnterDocument();
    }

    componentDidUpdate() {
        if(this.focused){
            setTimeout(function(){
                if(this._input) {
                    this._input.focus();
                }
            }.bind(this), 100);
        }

    }

    _inputChanged() {
        if (this.state.disabled)
            return false;

        if (this.lV !== this.value) {
            this.lV = this.value;
            return true;
        }

        return false;
    }
}


export default InputControl;


