import React from "react";
import format from 'date-fns/format';
import parse from 'date-fns/parse';
import isValid from 'date-fns/is_valid';

import Control from "../../Control";
import MaskedTextView from "./MaskedTextView";
import CalendarView from '../../calendar/CalendarView';

import PopOverView, {Edge, Position} from "../../popover/PopOverView";

class DatePickerView extends Control {

    constructor(props) {
        super(props)

        Object.assign(this.state, {
            pposition: Position.BOTTOM
        });
    }

    render() {
        return (
            <MaskedTextView textDidChange={this.handleTextChange.bind(this)}
                mask="99/99/9999" ref={(e) => this.txt = e}>
                <PopOverView className="v-DatePicker-Calendar"
                             position={this.state.pposition}
                             edge={Edge.CENTER}
                             ref={(e) => this._popover = e}>
                    <CalendarView ref={(e) => this.cal = e}
                                  displayedMonth = {this.state.value}
                                  value = {this.state.value}
                                  action={this.handleSelect.bind(this)}/>
                </PopOverView>
            </MaskedTextView>
        )
    }


    didEnterDocument() {
        super.didEnterDocument();
        this.txt._input.addEventListener('focus', this.onFocus);
    }

    didLeaveDocument() {
        this.txt._input.removeEventListener('focus', this.onFocus);
    }

    onFocus() {
        setTimeout(this.showCalendar.bind(this), 0);
    }

    onDoubleClick() {
        setTimeout(this.showCalendar.bind(this), 0);
    }

    showCalendar() {
        let rect = this.el.getBoundingClientRect(),
            offsetY = rect.top + rect.height;

        let position = Position.BOTTOM;
        if (offsetY + 230 > window.outerHeight) {
            position = Position.TOP;
        }

        this.setState({
            pposition: position
        });

        this._popover.show(this.txt);
    }

    handleTextChange(sender) {

        let value = parse(sender.value);
        if(isValid(value)) {
            this.value = value;
        }

    }

    handleSelect(sender) {
        this.value = sender.value;
        this._popover.close();

    }


    get value() {
        return this.state.value;
    }


    set value(v) {
        if(v instanceof Date) {
            this.txt.value = format(v, 'MM/DD/YYYY');

            this.setState({
                value : v
            });
        }
        else {
           this.txt.value = '';
        }


    }
}

export default DatePickerView;


