import React from "react";
import ReactDOM from "react-dom";

import KeyCodes from "../KeyCodes";
import ButtonView from "../button/ButtonView";
import PopOverView from "../popover/PopOverView";
import MenuView from "./MenuView";

import classNames from "classnames";

class MenuButtonView extends ButtonView {

    constructor(props) {
        super(props);
        this._menuClose = this._menuClose.bind(this);
    }

    get menu() {
        return this._menu;
    }

    get className() {
        return classNames('v-MenuButton', super.className)
    }

    render() {

        if (this.props.children) {
            if (this.props.tooltip) {
                return (
                    <button tabIndex={this.state.disabled ? '-1' : this.props.tabIndex}
                            className={this.className}>
                        <i className={classNames('icon', this.props.icon)}/>
                        <span className='label'>{this.props.text}</span>
                        <PopOverView ref={(e) => this._ttp = e}
                                     backgroundColor="rgb(26, 41, 55)" edge="center" position="top"
                                     className="v-Tooltip">
                            {this.props.tooltip}
                        </PopOverView>
                        <i className="chevron fa fa-chevron-down"/>
                        <MenuView {...this.props.children.props}
                                  onClose={this._menuClose}
                                  ref={(e) => this._menu = e}/>
                    </button>
                )
            }
            else {
                return (
                    <button tabIndex={this.state.disabled ? '-1' : this.props.tabIndex}
                            className={this.className}>
                        <i className={classNames('icon', this.props.icon)}/>
                        <span className='label'>{this.props.text}</span>
                        <i className="chevron fa fa-chevron-down"/>
                        <MenuView {...this.props.children.props}
                                  onClose={this._menuClose}
                                  ref={(e) => this._menu = e}/>
                    </button>
                )
            }
        }
        else {
            throw new Error("No MenuView specified in MenuButtonView");
        }
    }

    mouseDown(e) {
        e.preventDefault();
        e.stopPropagation();

        if (this.state.disabled)
            return;

        if (e.button === 0) {
            this._toggle();
        }
    }

    keyDown(e) {
        if (this.state.disabled)
            return;

        if (e.which === KeyCodes.KEY_ENTER || e.which === KeyCodes.KEY_SPACE) {
            this._toggle();
        }
    }

    mouseUp() {
        //empty
    }

    onClick(e) {
        e.stopPropagation();
        e.preventDefault();
    }


    _menuClose() {
        this.setState({active: false});
    }

    _toggle() {

        if (this.state.disabled)
            return;

        this.setState({
            active: !this.menu.state.open
        });

        if (!this.menu.state.open) {
            let el = this.el,
                popoverEl = ReactDOM.findDOMNode(this.menu._popover),
                rect = el.getBoundingClientRect(),
                top = rect.top,
                bottom = top + el.offsetHeight;

            if (bottom + popoverEl.offsetHeight < document.body.offsetHeight) {
                this.menu.show(
                    rect.left, bottom
                );
            }
            else if (top - popoverEl.offsetHeight > 0) {
                this.menu.show(
                    rect.left, top - popoverEl.offsetHeight - 1
                );
            }
            else {
                this.menu.show(rect.left, bottom);
            }
            this.menu.focus();
        }
        else {
            this.menu.close();
        }
    }

}


export default MenuButtonView;


