import React from "react";
import ReactDOM from "react-dom";
import isFunction from 'lodash/isFunction';

import View from "../View.js";
import Draggable from "../Draggable.js";
import Resizable from "../Resizable";
import ButtonView from "../button/ButtonView.js";
import EventResponder from "../EventResponder";

import classNames from "classnames";

import "./windowpanel.css";


/**
 *  window - free floating view with title and content
 *
 *
 * --- States ---
 * open
 * center
 * maximized
 * minimized
 * size
 * position
 *
 * --- Props ---
 * width
 * height
 * x
 * y
 * maximizable
 * minimizable
 * modal
 * draggable
 * resizable
 * title
 *
 *
 * To display a window call one of open(), center() or dropIn()
 *
 *
 */

class WindowPanel extends View {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            open: false,
            center: false,
            maximized: false,
            minimized: false,
            size: {
                width: props.width || 300,
                height: props.height || 300
            },
            position: {
                x: props.x || 0,
                y: props.y || 0
            }
        });


        this.close = this.close.bind(this);
        this.open = this.open.bind(this);
        this._toggle = this._toggle.bind(this);
        this.minimize = this.minimize.bind(this);

    }

    get className() {
        return classNames('v-WindowPanel', {
            'dropIn': this.state.dropIn,
            'v-WindowPanel-minimized': this.state.minimized
        }, super.className);
    }

    renderContent() {
        return this.props.children;
    }

    /**
     * Override to return different tool buttons in the window titlebar
     * @returns {Array}
     */
    get toolButtons() {
         let tools = [];
         if (this.props.maximizable) {
            tools.push(<ButtonView className="v-WindowPanel-toolBtn"
                                      key={1}
                                      action={this._toggle}
                                      iconOnly={true}
                                      icon={this.state.maximized ? 'fa fa-window-restore' : 'fa fa-window-maximize'}/>);
        }

        if (this.props.minimizable) {
            tools.push(<ButtonView className="v-WindowPanel-toolBtn"
                                      key={2}
                                      action={this.minimize}
                                      iconOnly={true} icon="fa fa-window-minimize"/>);
        }

        return tools;
    }


    renderTools() {

        return (
            <div className="v-WindowPanel-tools">
                <ButtonView action={this.close}
                            className="v-WindowPanel-toolBtn v-WindowPanel-close"
                            iconOnly={true} icon="fa fa-close"/>
                {this.toolButtons}
            </div>
        )

    }

    render() {

        let Window = (
            <Draggable handle="v-WindowPanel-titlebar" constrain={true} enabled={this.props.draggable} onDrag={this._dragCallback.bind(this)}>
                <Resizable minSize={this.props.minSize} maxSize={this.props.maxSize}
                           enabled={this.props.resizable}
                           onResizeFinish={() => EventResponder.postNotification('resizeFinish')}
                           onResize={this._resizeCallback.bind(this)}>
                    <View className={this.className} role="dialog" ref={(e) => this._window = e}>
                        <View className={classNames('v-WindowPanel-titlebar', {'v-draggable': this.props.draggable})}>
                            {this.renderTools()}
                            <div className="v-WindowPanel-title">{this.props.title}</div>
                        </View>
                        <div className="v-WindowPanel-content">
                            {this.renderContent()}
                        </div>
                    </View>
                </Resizable>
            </Draggable>
        );

        if (this.state.open) {
            if (this.props.modal) {
                return ReactDOM.createPortal(
                    <div className="v-WindowPanel-modalMask">
                        {Window}
                    </div>,
                    document.body
                );
            }
            else {
                return ReactDOM.createPortal(
                    Window, document.body
                )
            }
        }

        return null;

    }

    _resizeCallback(size) {
        this.state.size = size;
        EventResponder.postNotification("resize");
        if (isFunction(this.props.resize)) {
            this.props.resize.call(this, this.state.size);
        }
    }

    _dragCallback(p) {
        this.state.position = p;
        if (isFunction(this.props.drag)) {
            this.props.drag.call(this, this.state.position);
        }
    }

    didEnterDocument() {
        this.componentDidUpdate();
    }

    componentDidUpdate() {

        let window = this._window;
        let el = ReactDOM.findDOMNode(window);

        //set the size
        if (el) {
            el.style.width = this.state.size.width + 'px';
            el.style.height = this.state.size.height + 'px';
        }

        //set the position
        if (el) {
            el.style.left = this.state.position.x + 'px';
            el.style.top = this.state.position.y + 'px';
        }

    }

    close() {

        this.restore();
        this.setState({
            open: false,
            dropIn: false,
            maximized: false,
            minimized: false
        });

        EventResponder.postNotification('modalOff');
    }

    open() {
        this.setState({
            open: true,
            dropIn: false
        });

        if(this.props.modal) {
            EventResponder.postNotification('modalOn');
        }
    }

    dropIn(callback) {

        this.setState({
            position: {
                x: (document.body.offsetWidth - this.state.size.width) / 2.0,
                y: 0
            },
            open: true,
            dropIn: true
        });

        this.setState({
            position: {
                x: (document.body.offsetWidth - this.state.size.width) / 2.0,
                y: Math.max(50,
                    (document.body.offsetHeight - 150 - this.state.size.height) / 2.0)
            },
        });

        if(isFunction(callback)) {
            setTimeout(function(){
                callback.call(this);
            }.bind(this), 250);
        }


        setTimeout(function () {
            this.setState({
                dropIn: false
            })
        }.bind(this), 300);

        if(this.props.modal) {
            EventResponder.postNotification('modalOn');
        }
    }

    center() {

        this.setState({
            position: {
                x: (document.body.offsetWidth - this.state.size.width) / 2.0,
                y: (document.body.offsetHeight - 100 - this.state.size.height) / 2.0
            },
            open: true,
            dropIn: false
        });

        if(this.props.modal) {
            EventResponder.postNotification('modalOn');
        }

    }

    maximize() {

        this.lastState = {
            position: this.state.position,
            size: this.state.size,
            maximized: this.state.maximized,
            minimized: this.state.minimized
        };

        this.setState({
            position: {
                x: 0,
                y: 0
            },
            size: {
                width: document.body.offsetWidth - 2,
                height: document.body.offsetHeight - 2
            },
            maximized: true,
            minimized: false
        });
    }

    minimize() {

        this.lastState = {
            position: this.state.position,
            size: this.state.size,
            maximized: this.state.maximized,
            minimized: this.state.minimized
        };

        this.setState({
            maximized: false,
            minimized: true
        });
    }

    restore() {

        if (this.lastState) {
            this.setState(this.lastState);
            this.lastState = null;
        }
    }

    _toggle() {

        if (this.state.maximized) {
            this.restore();
        }
        else {
            this.maximize();
        }
    }

    setPosition(x, y) {
        this.setState({
            position: {
                x: x,
                y: y
            }
        })
    }

    setSize(w, h) {
        this.setState({
            size: {
                width: w,
                height: h
            }
        })
    }

}


WindowPanel.defaultProps = {
    draggable: true,
    resizable: true,
    maximizable: false,
    minimizable: false,
    width: 300,
    height: 300,
    x: 0,
    y: 0
};


export default WindowPanel;


