import View from "./View";
import isFunction from 'lodash/isFunction';

import classNames from "classnames";

import "./view.css";

/**
 *
 * View that enables scrolling
 *
 * --props--
 * scroll - callback function that is called when scrolling
 *
 */


class ScrollView extends View {
    get className() {
        return classNames('v-scrollview', super.className);
    }

    onScroll(e) {
        if(isFunction(this.props.scroll)) {
            this.props.scroll.call(this, e);
        }
    }

}


export default ScrollView;


