import React from "react";
import uniqueId from "lodash/uniqueId";

import View from "./View";


/**
 *
 *   DeckView
 *
 *   Used to swap out views using and index.
 *
 *
 */

class DeckView extends View {

    constructor(props) {
        super(props);

        Object.assign(this.state, {
            index: this.props.index || 0
        });

        this.id = uniqueId('DeckView-');
    }

    componentWillReceiveProps(nextProps) {
        if(nextProps.index !== this.props.index) {
            this.state.index = nextProps.index;
        }
    }

    get index() {
        return this.state.index;
    }

    set index(idx) {
        this.setState({
            index : idx
        });
    }


    get itemCount() {
        return React.Children.count(this.props.children);
    }

    render() {

        if (this.itemCount > 1) {
            return this.props.children[this.index];
        }

        return this.props.children;

    }

    didEnterDocument() {
        //insert DeckView DOM comment
        this.el.parentElement.insertBefore(document.createComment(this.id), this.el);
    }
}


export default DeckView;


